import React, { Component } from "react";
import {
  Form,
  Input,
  Select,
  Button,
  Table,
  Space,
  Card,
  message,
  Modal,
} from "antd";
import { ExclamationCircleOutlined } from "@ant-design/icons";
import "../../css/Juan/UserManagement.css";
import ExportJsonExcel from "js-export-excel";
import Axios from "../../util/Axios";

const { Option } = Select;
const { confirm } = Modal;
export default class UserManagement extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedRowKeys: [], // 选中的行的index
      selectedRows: [], //选中行的数据对象
      userData: [],
      columns: [
        {
          title: "姓名",
          dataIndex: "userName",
        },
        {
          title: "电话",
          dataIndex: "userPhoneNumber",
        },
        {
          title: "最后活跃时间",
          dataIndex: "lastActiveTime",
        },
        {
          title: "配送订单数",
          dataIndex: "deliveryOrderNumber",
        },
        {
          title: "所属站点",
          dataIndex: "siteName",
        },
        {
          title: "状态",
          dataIndex: "freezeStatus",
        },
        {
          title: "操作",
          key: "action",
          render: (record) => (
            <Space size="middle">
              <span  onClick={() => this.getDetail(record)}>
                查看
              </span>
              {/* <a href="#" onClick={() => this.froze(text, record)}>
                冻结
              </a> */}
            </Space>
          ),
        },
      ],
    };
  }
  componentDidMount() {
    this.renderAll();
  }

  //获取所有数据
  renderAll = () => {
    Axios.get("/User/findAll")
      .then((data) => {
        //获取后端的数据
        let { data: res } = data;
        console.log(res);
        console.log(res.data);
        if (res.code === "0001") {
          message.error("查询数据失败");
        } else if (res.code === "0000") {
          //处理从后端取得的数据
          let revisedData = this.reformData(res.data);
          console.log("====================================");
          console.log(revisedData);
          console.log("====================================");
          //2.修改State
          this.setState({
            userData: revisedData,
          });
        }
      })
      .catch((erro) => {
        console.log(erro);
      });
  };

  //处理后端数据的函数
  reformData = (data) => {
    let revisedData = data.map((item) => {
      let obj = {};
      obj.freezeStatus = item.freezeStatus;
      obj.deliveryOrderNumber = item.deliveryOrderNumber;
      obj.lastActiveTime = item.lastActiveTime;
      obj.siteName = item.siteName;
      obj.userId = item.userId;
      obj.userName = item.userName;
      obj.userPhoneNumber = item.userPhoneNumber;
      obj.key = item.userId;
      console.log("====================================");
      console.log(obj);
      console.log("====================================");
      return obj;
    });
    console.log(revisedData);
    return revisedData;
  };

  //多选
  onSelectChange = (selectedRowKeys, selectedRows) => {
    console.log("selectedRowKeys changed: ", selectedRowKeys);
    console.log("selectedRow changed: ", selectedRows);
    this.setState({ selectedRowKeys, selectedRows });
  };

  //冻结
  froze = () => {
    let { selectedRows } = this.state;
    // console.log(selectedRows);
    //搜索出选中的id
    let frozeData = selectedRows.map((item) => {
      return item.userId;
    });
    console.log(frozeData);

    //弹出框
    confirm({
      title: "你确定要冻结这些用户吗?",
      icon: <ExclamationCircleOutlined />,
      content: "冻结后，用户不能再进行订单操作",
      okText: "确定",
      cancelText: "取消",
      onOk: () => {
        console.log("确认冻结");
        Axios.post("/User/freezeUser", { id: frozeData, freezeStatus: "冻结" })
          .then((data) => {
            console.log(data);
            //重新获取后端的数据，重新把数据渲染进table
            this.renderAll();
          })
          .catch((erro) => {
            console.log(erro);
          });
      },
      onCancel: () => {
        console.log("取消冻结");
      },
    });
  };

  //解冻
  thawUser = () => {
    let { selectedRows } = this.state;
    // console.log(selectedRows);
    //搜索出选中的id
    let thawData = selectedRows.map((item) => {
      return item.userId;
    });
    console.log(thawData);

    //弹出框
    confirm({
      title: "你确定要解冻这些用户吗?",
      icon: <ExclamationCircleOutlined />,
      content: "解冻后，用户可以进行订单操作",
      okText: "确定",
      cancelText: "取消",
      onOk: () => {
        console.log("确认解冻");
        Axios.post("/User/thawUser", { id: thawData, freezeStatus: "解冻" })
          .then((data) => {
            console.log(data);
            //如果修改成功，重新获取后端的数据，重新把数据渲染进table
            this.renderAll();
          })
          .catch((erro) => {
            console.log(erro);
          });
      },
      onCancel: () => {
        console.log("取消解冻");
      },
    });
  };

  //导出excel表
  exportExcel = () => {
    const datas = this.state.userData ? this.state.userData : ""; //表格数据
    var option = {};
    let dataTable = []; //新建数组放数据
    console.log(datas);
    if (datas) {
      for (const data of datas) {
        console.log(data);
        if (data) {
          let obj = {
            key: data.userId,
            userName: data.userName,
            userPhoneNumber: data.userPhoneNumber,
            lastActiveTime: data.lastActiveTime,
            deliveryOrderNumber: data.deliveryOrderNumber,
            siteName: data.siteName,
            freezeStatus: data.freezeStatus,
          };
          dataTable.push(obj);
        }
      }
    }
    console.log(dataTable);
    option.fileName = "会员信息表"; //文件名
    option.datas = [
      {
        sheetData: dataTable, //数据
        sheetName: "会员信息表",
        sheetHeader: [
          "编号",
          "姓名",
          "电话",
          "最后活跃时间",
          "配送订单数",
          "所属站点",
          "状态",
        ], // 第一行
      },
    ];
    var toExcel = new ExportJsonExcel(option);
    toExcel.saveExcel(); //保存
  };

  //搜索功能
  onFinish = (values) => {
    // console.log("Success:", values);
    //把默认值写进values
    values.freezeStatus === undefined
      ? (values.freezeStatus = "解冻")
      : (values.freezeStatus = values.freezeStatus);
    values.site === undefined
      ? (values.site = "华阳店")
      : (values.site = values.site);
    console.log(values);
    //axios发送给后台搜索
    Axios.post("/User/findByLike", {
      SiteName: values.site,
      freezeStatus: values.freezeStatus,
      keyWords: values.keywords,
    })
      .then((data) => {
        //获取后端的数据
        let { data: res } = data;
        // console.log("=========后端返回的搜索数据============");
        // console.log(res);
        // console.log(res.data);
        //如果查到数据
        if (res.code === "0000") {
          //处理后端的数据
          let revisedData = this.reformData(res.data);
          //修改State
          this.setState({
            userData: revisedData,
          });
        }
        //如果没有查到数据
        if (res.code === "0001") {
          message.error("没有查询到数据，请修改您的搜索条件");
        }
      })
      .catch((erro) => {
        console.log(erro);
      });
  };

  //跳转详情页
  getDetail = (record) => {
    console.log(record);
    this.props.history.push({
      pathname: "/index/juanJ",
      query: { record },
    });
  };

  render() {
    const { selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <div>
        <Form id="userManagement-search" name="search" onFinish={this.onFinish}>
          <Form.Item label="关键字：" htmlFor="keywords" name="keywords">
            <Input id="keywords" placeholder="请输入用户名或电话" />
          </Form.Item>

          <Form.Item
            label="状态："
            htmlFor="userManagement-freezeStatus"
            name="freezeStatus"
          >
            <Select defaultValue="解冻" id="userManagement-freezeStatus">
              <Option value="解冻">解冻</Option>
              <Option value="冻结">冻结</Option>
            </Select>
          </Form.Item>

          <Form.Item label="站点：" htmlFor="site" name="site">
            <Select id="site" defaultValue="华阳店">
              <Option value="华阳店">华阳店</Option>
              <Option value="武侯店">武侯店</Option>
              <Option value="玉林店">玉林店</Option>
            </Select>
          </Form.Item>
          <Form.Item>
            <Button htmlType="submit" name="submit">
              搜索
            </Button>
          </Form.Item>
        </Form>
        <Card>
          <Button onClick={this.exportExcel}>导出会员信息Excel表</Button>
          <Button id="userManagement-froze" onClick={() => this.froze()}>
            冻结
          </Button>
          <Button id="userManagement-froze" onClick={() => this.thawUser()}>
            解冻
          </Button>
        </Card>

        <Table
          rowSelection={rowSelection}
          columns={this.state.columns}
          dataSource={this.state.userData}
          size="small"
        />
      </div>
    );
  }
}
