import React, {Component} from 'react'
import "../css/wmx-css/wmx-ChangePassword.css"
import {Input,Button} from 'antd';
import {Link} from  'react-router-dom'
import Axios from "../util/Axios";
class ChangePassword extends Component {
    constructor(props) {
        super(props);

        this.input_Y =React.createRef();
        this.input_X =React.createRef();
        this.input_Q =React.createRef();
        this.state ={
            YPwd:"",
            XPwd:"",
            QPwd:""
        }
    }

    affirm =() =>{
        console.log(" 发送成功密码");
        console.log(this.input_Y.current.state.value);
        console.log(this.input_X.current.state.value);
        console.log(this.input_Q.current.state.value);
        // 发送请求确认密码
        Axios.post('/sysUser/update',{
            staffNumber:this.input_Y.current.state.value,
            staffPassword:this.input_X.current.state.value,
            newStaffPassword:this.input_Q.current.state.value
        }).then((data) =>{
            console.log(data)
        }).catch((err) =>{
            console.log(err)
        })

    };
    render() {

        return (
            <div className="wmx-center">
                <div className="wmx-pad">
                    <label className="wmx-pad">账户:</label>
                    <Input placeholder="请输入原密码" style={{width:300}} defaultValue={this.state.YPwd} ref ={this.input_Y}/>
                </div>

                <div className="wmx-pad">
                    <label className="wmx-pad">密码:</label>
                    <Input placeholder="请输入新密码" style={{width:300}} defaultValue={this.state.XPwd} ref ={ this.input_X} />
                </div>
                <div className="wmx-pad">
                    <label className="wmx-pad-P">新密码:</label>
                    <Input placeholder="请确认密码" style={{width:300}} defaultValue={this.state.QPwd} ref ={ this.input_Q}/>
                </div>
                <Link to={"/login"} >
                    <Button className="wmx-Btn-B" onClick={() =>this.affirm()}>确认</Button>
                </Link>

            </div>
        )
    }
}

export default ChangePassword