import React, { Component } from "react";
import { Button, Form, Input, TimePicker } from "antd";
import "moment/locale/zh-cn";
import locale from "antd/lib/date-picker/locale/zh_CN";
import "../../css/haojie/SiteManagementAddSite.css";
import QRCode from "qrcode.react";
import AAA from "./AAA";
import moment from "moment";

import Axios from "axios";

const { RangePicker } = TimePicker;

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
const tailLayout = {
  wrapperCol: { offset: 8, span: 16 },
};

const dateFormat = "HH:mm" || undefined;

class AddSite extends Component {
  constructor(props) {
    super(props);
    this.state = {
      message: "",
      msg: 0,
      sTime: "",
      startTime: undefined, //开始时间
      endTime: undefined, //结束时间
    };
  }

  getSonMess(msg) {
    console.log("传递过来的值" + msg);
    console.log(this);
  }
  componentDidUpdate() {}

  goBack() {
    this.props.history.push({ pathname: "/index/haoJie",});
    console.log("点击了");
  }
  state = {
    size: "large",
  };
  onFinish = (values) => {
    console.log("Success:", values);
    Axios.post("/site/add", {
      siteAddress: values.siteAddress,
      siteCoordinate: values.siteCoordinate,
      siteLimits: values.siteLimits,
      siteName: values.siteName,
      sitePhoneNumber: values.sitePhoneNumber,
      siteRepresent: values.siteRepresent,
      siteTime: this.state.sTime,
    })
      .then((data) => {
        console.log(data, "添加成功");
      })
      .catch((err) => {
        console.log(err, "添加失败");
      });
  };

  onOk = (value) => {
    console.log("onOk: ", value);
    value = moment(value).format("HH:mm:ss"); //这么解决
  };
  onChange = (value, dateString) => {
    console.log("Selected Time: ", value);
    console.log("Formatted Selected Time: ", dateString);
    this.setState({
      startTime: dateString[0],
      endTime: dateString[1],
    });
    const siteTime = dateString[0] + "-" + dateString[1];
    this.setState({
      sTime: siteTime,
    });

    console.log(siteTime, "=======================");
  };
  //切换tab的时候的回调函数
  tabChange = (activeKey) => {
    this.setState({
      startTime: undefined, //开始时间
      endTime: undefined, //结束时间
    });
  };

  render() {
    const { size } = this.state;

    return (
      <div>
        <Button size={size} onClick={this.goBack.bind(this)}>
          返回
        </Button>
        <hr />
        {/* 二维码 */}
        <div className="AddSite-tweoWeiMa">
          <QRCode value="http: /lfacebook.github.io/react/" />
        </div>

        <Form
          {...layout}
          initialValues={{ remember: true }}
          onFinish={this.onFinish}
        >
          <div className="AddSite-sp">
            <span className="AddSite-iF">基本信息:</span>
            {/* 基本信息 */}

            <div className="AddSite-inF">
              <Form.Item
                label="站点名称"
                rules={[{ required: true }]}
                name="siteName"
              >
                <Input maxLength={25} />
              </Form.Item>

              <Form.Item
                label="站店电话"
                rules={[{ required: true }]}
                name="sitePhoneNumber"
              >
                <Input maxLength={25} />
              </Form.Item>

              <Form.Item
                label="地址"
                rules={[{ required: true }]}
                name="siteAddress"
              >
                <Input maxLength={25} />
              </Form.Item>

              <Form.Item
                label="描述:"
                rules={[{ required: true }]}
                name="siteRepresent"
              >
                <Input maxLength={25} />
              </Form.Item>

              <Form.Item
                label="接单时间:"
                rules={[{ required: true }]}
                name="siteTime"
              >
                <RangePicker
                  placeholder={["开始时间", "结束时间"]}
                  onChange={this.onChange}
                  value={
                    this.state.startTime === undefined ||
                    this.state.endTime === undefined ||
                    this.state.startTime === "" ||
                    this.state.endTime === ""
                      ? null
                      : [
                          moment(this.state.startTime, dateFormat),
                          moment(this.state.endTime, dateFormat),
                        ]
                  }
                  format={dateFormat}
                  onOk={this.onOk}
                  locale={locale}
                />
              </Form.Item>
            </div>
            {/* 位置信息 */}
            <div className="AddSite-addIn">
              <span className="AddSite-iF">位置信息:</span>
              <div className="AddSite-addInf">
                <Form.Item
                  label="在地图上选择定位:"
                  rules={[{ required: true }]}
                  name="siteCoordinate"
                >
                  <Input maxLength={25} />
                </Form.Item>
              </div>
            </div>

            {/* 配送范围 */}
            <div className="AddSite-fayF">
              <span className="AddSite-iF">配送范围:</span>
              <div className="AddSite-fayDes"></div>
            </div>

            <div className="AddSite-buttombox">
              <Form.Item
                className="AddSite-aaaa"
                label="常温产品时效描述:"
                rules={[{ required: true, message: "" }]}
                name="statusCode"
              >
                <Input />
              </Form.Item>
              <div className="AddSite-changWen">常温产品配送范围设置</div>
              <div className="AddSite-peiSongG">
                <span className="AddSite-span">方圆</span>{" "}
                <Form.Item name="siteLimits">
                  <Input className="AddSite-juLi" />
                </Form.Item>
                <span className="AddSite-span1">公里</span>
              </div>
              <div id="AddSite-container">
                <AAA sonMess={this.getSonMess.bind(this)} />
              </div>
            </div>
            <Form.Item {...tailLayout}>
              <Button
                htmlType="submit"
                type="button"
                className="AddSite-AddSave"
              >
                保存
              </Button>
            </Form.Item>
          </div>
        </Form>
      </div>
    );
  }
}

export default AddSite;
