import React, {Component} from 'react'
import {Button, Divider, Table, Typography, } from "antd";
import { Input } from 'antd';
import '../../css/ZhanDianHouTai/F_reviewed.css'
import { Select,  } from 'antd';
import Axios from '../../util/Axios'
// import {Link} from 'react-router-dom'
// import moment from 'moment';
import {withRouter} from "react-router-dom";
// const dateFormat = 'YYYY-MM-DD';
const {Title} = Typography;
const { Option } = Select;
const columns = [
    {
        title: '商品1级分类',
        dataIndex: 'oneName',
    },
    {
        title: '商品2级分类',
        dataIndex: 'twoName',
    },
    {
        title: '数量',
        dataIndex: 'count',
    },
];
const data = [
    {
        key: '1',
        classOne: 'John Brown',
        classTwo: 32,
        count: 'New York No. 1 Lake Park',
    },
    {
        key: '2',
        classOne: 'Jim Green',
        classTwo: 42,
        count: 'London No. 1 Lake Park',
    },
    {
        key: '3',
        classOne: 'Joe Black',
        classTwo: 32,
        count: 'Sidney No. 1 Lake Park',
    },
];

class F_reviewed extends Component {
    constructor(props){
        super(props);

        this.state={
            myAllData:[],
            orderDetailsMsg: '',//接受订单管理页面发送的数据
            orderDetails:'',//订单详情表格的数据
            mydisabled:true,
            orderNum:[1],
            orderComment:[],
            orderMoney:[],
            orderContent:[],
            myOrderNum:[],
            columns : [
                {
                    title: '商品1级分类',
                    dataIndex: 'classification1',
                    align: 'center'
                },
                {
                    title: '商品2级分类',
                    dataIndex: 'classification2',
                    align: 'center'
                },
                {
                    title: '数量',
                    dataIndex: 'goodsNum',
                    align: 'center'
                }
            ]
        }
    }

    // 初始化订单详情发送过来的数据
    componentDidMount() {
    let item=this.props.location.query.orderId;
    Axios.post("/deliveryDetails",{orderId:item}).then(data=>{
        console.log(data.data.data);
        let orderComment=[];
        let orderMoney=[];
        let orderContent=[];
        let myOrderNum=[];
        let count=0;
        let heavy=0;
        let price=0;
        data.data.data.map((item,index) => {
           orderComment.push({
               key:index,
                auditSituation:item.auditSituation,//审核描述
               checkStatus:item.checkStatus,//审核状态
               consigneeAddress:item.consigneeAddress,//地址
               consigneeName:item.consigneeName,//收货人姓名
               consigneePhoneNumber:item.consigneePhoneNumber,//收货人电话号码
               street:item.street,//所属街道
               userName:item.userName,//用户姓名
               orderId:item.orderId,
               logisticsId:item.orderDetails[0].logisticsId,
           });
           orderMoney.push({//delivery是个数组对象
               key:index,
               cashNumber:item.delivery[0].cashNumber,//收银台号
               serialNumber:item.delivery[0].serialNumber,//流水单号
               streamTime:item.delivery[0].streamTime,//购物凭条的日期
           });
           orderContent.push({
               key:index,
               logisticsId:item.orderDetails[0].logisticsId,
                oneName:item.oneName,
               twoName:item.twoName,
               threeName:item.threeName,
               supId:item.supId,//对应的物料id
               supModel:item.supModel,//物料型号
               supName:item.supName,//物料名称
           });
            myOrderNum.push({
                count:item.orderDetails[0].count,
                heavy:item.orderDetails[0].heavy,
                price:item.orderDetails[0].price
            });
            count+=item.orderDetails[0].count;
            heavy+=item.orderDetails[0].heavy;
            price+=item.orderDetails[0].price;
        });
        this.setState({
            myAllData:data.data.data,
            orderComment:orderComment[0],
            orderMoney:orderMoney[0],
            orderContent:orderContent,
            myOrderNum:myOrderNum,
            count:count,
            heavy:heavy,
            price:price
        })
    })

    }

    //返回上一页
    goBackPage= () => {
        window.history.back(-1)
    };
//===================拆单选择==============
     handleChange=(value)=> {
        // console.log(this.selectValue.current);
         console.log(value);
         if (value==='is'){
             this.setState({
                 mydisabled:false,
                 // orderNum:[1,2,3]
             })
         } else {
             this.setState({
                 mydisabled:true,
                 orderNum:[1]

             })
         }
    };
    handleChangeOne=(value)=>{
        console.log(value.label);
        let ordernum=value.label;
        let orderArr=[];
        console.log(ordernum);
        for (let i=1;i<=ordernum;i++){
            orderArr.push(i);
            if (i===ordernum){
                console.log(orderArr);
                setTimeout(this.changeOrder(orderArr),2000)
            }
        }
    };
changeOrder=(orderArr)=>{
    console.log(orderArr);
    let items=[...this.state.orderNum];
    items=orderArr;
    this.setState({
        orderNum:items
    });
};
makeSure=(data)=>{
    // let that=this;
    let checkStatus='',
        orderId=this.state.orderComment.orderId;
    let myItem=this.state.myAllData;
    if (data==='sure'){
        myItem.forEach((item,index)=>{
            item.checkStatus="审核成功";
            item.auditSituation="审核成功";
            return (checkStatus='审核成功')
        });
    }else {
        myItem.forEach((item,index)=>{
            item.checkStatus="审核失败";
            item.auditSituation="超出配送范围";
            return (checkStatus='审核失败')
        });
    }
    console.log(myItem);
Axios.put("/deliveryDetails",myItem).then(data=>{
    console.log(data);
    if (data.status===200) {
        this.props.history.push({pathname:'/index/scuccess',query:{checkStatus:checkStatus,orderId:orderId}})
    }

})
};
    render() {
        // console.log(this.state.orderNum)
        //控制是否有分页显示
    console.log(this.state.orderComment);
        let paginationProps = '';
    let myTime=this.state.orderMoney.streamTime;
        console.log(myTime);
        if (this.state.orderDetails.length >= 10) {
            paginationProps = true
        } else {
            paginationProps = false
        }

        let myArr=this.state.orderNum.map((value,index)=>{
            return ( <div key={index}>
                <div className='chooseOrder'>
                    <p>物流订单：{this.state.orderComment.logisticsId}</p>
                    <label htmlFor="">选择时效产品</label>
                    <Select labelInValue defaultValue="num1" style={{ width: 120 ,marginLeft:5,marginRight:5}} disabled={this.state.mydisabled}>
                        <Option value ="num1">生鲜</Option>
                        <Option value="num2">常温</Option>
                    </Select>
                    {/*<p>(地址不在配送范围内)</p>*/}
                </div>
                <p>填写订单明细</p>
                <div className='chooseTable'>
                    <div>
                        <Table columns={columns} dataSource={this.state.orderContent} size="middle" />
                    </div>
                    <div className='F_num'>
                        <label>箱数：</label>
                        <Input placeholder={this.state.count} style={{width:200,marginRight:100}}/>
                        <br/>
                        <label>重量：</label>
                        <Input placeholder={this.state.heavy} style={{width:200}}/>
                        <br/>
                        <label>金额：</label>
                        <Input placeholder={this.state.price} style={{width:200}}/>
                    </div>
                    <div className='F_liebie'>
                        <label htmlFor="">类别：</label>
                        <Select defaultValue="num1" style={{ width: 120 }}>
                            <Option value="num1">1</Option>
                            <Option value="num2">2</Option>
                            <Option value="num3">3</Option>
                            <Option value="num4">4</Option>
                        </Select>
                        <br/>
                        <label>型号：</label>
                        <Input placeholder="Basic usage" style={{width:200}}/>
                        <br/>
                        <label>数量：</label>
                        <Input placeholder="Basic usage" style={{width:200}}/>
                    </div>

                </div>
            </div>)
        });

        return (
            <div id='r_details'>
                <div className='r_detailsTitle'>
                    <Button size="small" onClick={this.goBackPage}>返回</Button>
                    <p>订单状态：{this.state.orderComment.checkStatus}</p>
                </div>
                <Divider/>
                <div id='r_detailsMsg'>
                    <div>
                        <label htmlFor="">订单编号：</label>
                       <Input placeholder={this.state.orderComment.orderId} style={{width:200}}/>
                    </div>
                    <div>
                        <label htmlFor="">用户：</label>
                        <Input placeholder={this.state.orderComment.userName} style={{width:200}}/>
                    </div>
                    <div>
                        <label htmlFor="">收货人：</label>
                        <Input placeholder={this.state.orderComment.consigneeName}style={{width:200}}/>
                    </div>
                    <div>
                        <label htmlFor="">电话：</label>
                        <Input placeholder={this.state.orderComment.consigneePhoneNumber} style={{width:200}}/>
                    </div>
                    <div>
                        <label htmlFor="">收货地址：</label>
                        <Input placeholder={this.state.orderComment.consigneeAddress} style={{width:200}} />
                    </div>
                </div>
                <div className='r_address'>
                    <p>所属街道：{this.state.orderComment.street}</p>
                </div>
                <Divider/>

                <div id="r_shoppingVoucher">
                    <Title level={4}>购物凭条：</Title>
                    <div>
                        <p>收银条台号：</p>
                        <Input placeholder={this.state.orderMoney.cashNumber} style={{width:200}}/>
                    </div>
                    <div>
                        <p>流水号：</p>
                        <Input placeholder={this.state.orderMoney.serialNumber} style={{width:200}}/>
                    </div>
                    <div>
                        <p>日期：</p>
                        <Input placeholder={this.state.orderMoney.streamTime} style={{width:200}}/>
                    </div>
                </div>
                <Divider/>
                <div id='r_orderDetails'>
                    <div>
                        <p>派单选择</p>
                        <label htmlFor="">选择是否拆单：</label>
                        <Select defaultValue="not" style={{ width: 120,marginRight:80 }} onChange={this.handleChange} >
                            <Option value="is">是</Option>
                            <Option value="not">否</Option>
                        </Select>
                        <label htmlFor="">选择拆单数量：</label>
                        <Select labelInValue  defaultValue={{ value: 'num1' }} style={{ width: 120 }} onChange={this.handleChangeOne} disabled={this.state.mydisabled}>
                            <Option value="num1">1</Option>
                            <Option value="num2">2</Option>
                            <Option value="num3">3</Option>
                            <Option value="num4">4</Option>
                        </Select>
                    </div>
                    {myArr}
                    <div id='r_toPeiSon'>
                            {/*<Link to={{pathname:'/ReviewedSure',query:{checkStatus:'通过',orderId:this.state.orderComment.orderId}}}>*/}
                            <Button style={{marginRight:100}} onClick={()=>{this.makeSure('sure')}}>确认</Button>
                            {/*</Link>*/}

                        {/*<Link to={{pathname:'/ReviewedSure',query:{checkStatus:'未通过',orderId:this.state.orderComment.orderId}}}>*/}
                            <Button style={{marginRight:100}} onClick={()=>{this.makeSure('false')}}>拒绝</Button>
                        {/*</Link>*/}
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(F_reviewed)
