import React, { Component } from "react";
import { Button, Form, Input, Select, Col } from "antd";
import Axios from "../../util/Axios";
const { Option } = Select;

function handleChange(value) {
  console.log(`selected ${value}`);
}
const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
const tailLayout = {
  wrapperCol: { offset: 8, span: 16 },
};

class AddSiteMembers extends Component {
  constructor(props) {
    super(props);
    this.state = {
      size: "large",
      Num: "",
    };
  }

  handleSizeChange = (e) => {
    this.setState({ size: e.target.value });
  };
  componentDidMount() {
    console.log(this.props.location.state.siteId);
    console.log(this.props.location.state.staffId);
    this.setState({
      Num: this.props.location.state.siteId[0],
    });
  }

  goBack() {
    this.props.history.push({
      pathname: "/index/haoJie",
      state: { siteId: this.props.location.state.siteId[0] },
    });
    console.log("点击了");
  }
  onFinish = (values) => {
    console.log("Success:", values.name);
    console.log("Success:", values.phone);
    console.log("Success:", values.siteName);
    console.log("Success:", this.props.location.state.siteId);
    Axios.post("/site/addSiteMember", {
      name: values.name,
      phone: values.phone,
      siteName: values.siteName,
      // siteId: this.props.location.state.siteId,
      siteId: this.props.location.state.siteId[0],
    })
      .then((data) => {
        console.log(data, "添加成功");
      })
      .catch((err) => {
        console.log(err, "添加失败");
      });
  };
  render() {
    const { size } = this.state;

    return (
      <div>
        <Button size={size} onClick={this.goBack.bind(this)}>
          返回
        </Button>
        <hr />
        <span>基本信息</span>
        <div>
          <Form
            {...layout}
            initialValues={{ remember: true }}
            onFinish={this.onFinish}
            name="JBXX"
          >
            <Form.Item
              label="站店员姓名"
              name="name"
              rules={[{ required: true, message: "" }]}
            >
              <Col span={8}>
                <Input maxLength={25} />
              </Col>
            </Form.Item>

            <Form.Item
              label="站店员电话"
              rules={[{ required: true, message: "" }]}
              defaultValue="Hello"
              name="phone"
            >
              <Col span={8}>
                <Input />
              </Col>
            </Form.Item>
            <Form.Item
              label="所属站点"
              rules={[{ required: true, message: "" }]}
              name="siteName"
            >
              <Select
                defaultValue="青羊店"
                style={{ width: 120 }}
                onChange={handleChange}
              >
                <Option value="青羊店">青羊店</Option>
                <Option value="金牛店">金牛店</Option>
                <Option value="武侯店">武侯店</Option>
                <Option value="犀浦店">犀浦店</Option>
                <Option value="三圣乡店">三圣乡店</Option>


              </Select>
            </Form.Item>

            <Form.Item {...tailLayout}>
              <Button
                type="primary"
                htmlType="submit"
                style={{ marginRight: "5px" }}
              >
                提交
              </Button>
            </Form.Item>
          </Form>
        </div>
      </div>
    );
  }
}

export default AddSiteMembers;
