import React from 'react';
import './css/wmx-css/wmx_normalize.css'
import './App.css';

import {
    HashRouter as Router,
    Route,
    Redirect,
    Switch,
} from 'react-router-dom'


// 主页
import Index from "./page/Index";
// 登录页
import Login from "./page/Login";
// 物料管理模块
import MaterialManagement from "./component/MaterialManagement";
//  物料统计
import MaterialStatistics from "./component/MaterialStatistics";
// 基础设置模块
import BasicSetment from "./component/BasicSetment";
// 配送设置模块
import DeliverySetment from "./component/DeliverySetment";
// 添加模块
import Addition from "./component/Addition";
// 修改密码
import ChangePassword from "./component/ChangePassword";
// 商品品类管理
import OrderManagement from "./component/thr/GoodManagement";
import Addgoodlist from "./component/thr/Addgoodlist";

// 派送订单管理
import OrderManagementZ from "./component/Zj/OrderManagementZ";
import OrderDetails from "./component/Zj/OrderDetails";

// 用户管理
import UserManagement from "./component/Juan/UserManagement";
import UserDetail from "./component/Juan/UserDetail";
// 操作管理
import Operator from "./component/LiKeliang/Operator";
import Journal from "./component/LiKeliang/Journal";
import Role from "./component/LiKeliang/Role ";
// 配送员管理
import Distribution from "./component/zhao/Distribution";
// 站点管理
import SiteManagement from "./component/LiHaoJie/SiteManagement";
import AddSite from "./component/LiHaoJie/AddSite";
import SiteAccountManagement from "./component/LiHaoJie/SiteAccountManagement";
import AddSiteMembers from "./component/LiHaoJie/AddSiteMembers";

//=============================站点后台=========================
// 审核管理
import DistributionTop from "./component/client-Side/DistributionTop";

// 配送管理
import DeliveryList from "./component/client-Side/DeliveryList";

// 配单管理
import FindReadList from "./component/client-Side/ZJ/FindReadList";
import DispatchDetails from "./component/client-Side/ZJ/DispatchDetails";
import Distribute from "./component/client-Side/ZJ/Distribute";
import NewLogist  from './component/client-Side/ZJ/NewLogistics'
import DistributionEditor from "./component/client-Side/ZJ/DistributionEditor";
import Reviewd_sure from "./component/client-Side/Reviewd_sure";
import F_reviewed from "./component/client-Side/F_reviewed";
function App() {
  return (

          <Router>
              <div className="App">

                  <Switch>
                      <Route path="/" exact  render= {()=><Redirect to={"/login"}/>}/>
                      <Route path="/login" component ={Login}/>
                      <Route  path="/index" render={() =>
                            <Index>
                                {/*基础设置模块*/}
                                <Route exact path="/index/B" component={BasicSetment}/>
                                {/*  物料管理模块*/}
                                <Route  exact path="/index/m" component={MaterialManagement}/>
                                {/*   统计物料  */}
                                <Route exact path="/index/s" component={MaterialStatistics}/>
                                {/*  配送设置模块*/}
                                <Route exact path="/index/P" component={DeliverySetment}/>
                                 {/*     添加模块*/}
                                <Route exact path="/index/Addition/0" component = {Addition}/>
                                {/*    修改密码 */}
                                <Route  path="/index/ChangePassword" component = {ChangePassword}/>
                                {/*   商品品类管理模块 */}
                                <Route path="/index/lei" component={OrderManagement} />
                                <Route path="/index/Add" component={Addgoodlist}/>
                                 {/*  配送订单管理  */}
                                <Route path="/index/Zj" component={OrderManagementZ}/>
                                <Route path="/index/Zjj" component={OrderDetails}/>
                                {/*     用户管理  */}
                                <Route path="/index/juan" component={UserManagement}/>
                                <Route path="/index/juanJ" component={UserDetail}/>
                                {/*   系统管理  */}
                                <Route path={"/index/ke"} component={Operator}/>
                                <Route path={"/index/data"} component={Journal}/>
                                <Route path={"/index/role"} component={Role}/>
                                 {/*   配送园管理 */}
                                <Route path={"/index/zhao"} component={Distribution}/>
                                {/*   站点管理 */}
                                <Route path={"/index/haoJie"} component={SiteManagement}/>
                                <Route path={"/index/Site"} component={AddSite}/>
                                <Route path={ "/index/SiteAccountManagement"} component={SiteAccountManagement}/>
                                <Route path={ "/index/AddSiteMembers"} component={AddSiteMembers}/>

                                {/*   ==============================站点管理===================== */}
                                {/* 审核管理    */}
                                <Route path={"/index/audit"} component={DistributionTop}/>
                                <Route path={"/index/scuccess"} component={Reviewd_sure}/>
                                <Route path={"/index/dai"} component={F_reviewed}/>

                                {/*  配送管理  */}
                                <Route path={"/index/Delivery"} component={DeliveryList}/>
                                <Route path={"/index/DistributionEditor"} exact component={DistributionEditor}/>
                                <Route path={'/index/Distribute'} component={Distribute}/>
                                <Route path={'/index/DispatchDetails'} component={DispatchDetails}/>
                                 {/*  配单管理  */}
                                <Route path={"/index/List"} exact component={FindReadList}/>
                                <Route path={'/index/NewLogistics'} component={NewLogist}/>

                            </Index>
                          }>
                      </Route>

                  </Switch>

              </div>
          </Router>


  );
}

export default App;
