import React, { Component } from 'react'
import { Input,  Button, Space, Table, Form, Row, Col, Popconfirm,Modal } from 'antd';
// import ExportJsonExcel from "js-export-excel"; //引入到处excel表格的模块
import 'moment/locale/zh-cn';
// import locale from 'antd/es/date-picker/locale/zh_CN';
import '../../css/thr/OrderManagement.css';
import Axios from"../../util/Axios.js";
import { Link } from "react-router-dom";


const { confirm } = Modal;
// const { Option } = Select;
// const { RangePicker } = DatePicker;
// const {initialValues} = Form


class OrderManagement extends Component {
  constructor(props) {
    super(props);
    this.state = {
	  goodsList:'',
	  inputnum:"",
      selectedRowKeys: [],//选中行的index
      selectedRows: [], //选中行的数据对象
      orderData: [],//订单列表的数据
      startDistribution: '',
      endDistribution: '',
      columns: [      
        { title: '商品大类', dataIndex: 'goodsALLCategory', align: 'center' },
		{ title: '商品细类', dataIndex: 'goodsCategory', align: 'center' },
		{ title: '商品名称', dataIndex: 'goodsName', align: 'center' },
		
        { title: '商品数量', dataIndex: 'goodsQuantity', align: 'center' },
       
        {
          title: '操作',
          dataIndex: 'operation',
          align: 'center',
          render: (text, record, index) =>
            <Space className='operationBtns'>
              <Button type="link" size='small' onClick={() => this.getOrderMsg(record)}>修改</Button>
              <Popconfirm okText="删除" cancelText="取消" title="确定删除吗?" onConfirm={() => this.handleDelete(record.key)}>
                <Button type='link'>删除</Button>
              </Popconfirm>

            </Space>
        },
      ]

    }
  }

  UNSAFE_componentWillMount() {
  
  }
  componentDidMount(){
	 this.getgoodlist();
  }
  getgoodlist = () =>{
	  Axios.get("/category").then(data=>{
	  		  console.log(data);
	  		  const mydata = [];
	  		  console.log(data.data.data);
	  		 data.data.data.forEach((val)=>{
	  			 mydata.push({
	  			key: val.threeId,
	  			goodsALLCategory:val.oneName,
	  			goodsCategory:val.twoName,
	  			goodsName:val.threeName,
	  			goodsQuantity: val.categoryNumber
	  			})
	  		 });
	  		 console.log(mydata);
	  		 this.setState({
	  		   orderData: mydata,
	  		 });
	  		  
	  })
  }
  
  //表格行的多选和全选
  onSelectChange = (selectedRowKeys, selectedRows) => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    console.log(selectedRows);
    this.setState({ selectedRowKeys:selectedRowKeys	
	, selectedRows:selectedRows });
	
  };
  //导出excel表
 

  //发送请求把订单列表该行的数据发送到下一个页面，并且跳转页面
  getOrderMsg = (record) => {
	  this.setState({
		  inputnum:record.goodsQuantity
		 
	  })
	  let goods=<div>
	  <div>商品名称:<span style={{color:"#007FFF",paddingLeft:20}} >{record.goodsName}</span></div>
	  <div style={{paddingTop:20}} ><Input onChange={this.changeGoodsNum}></Input></div>
	  </div>
	  confirm({
	      title: '修改商品数量',
			okText:'确认',
			cancelText:'取消',
	      content:goods ,
	      onOk:()=> {
			  
			  Axios.post("/category/updateCategory",{				 
				   threeId:record.key,
					   categoryNumber:this.state.inputnum			  
			  }).then(()=>{
			   this.getgoodlist ();	  		  
			  }) ;
	        console.log('OK');
			console.log(record.key);
			console.log(this.state.inputnum)
			
	      },
	      onCancel:()=> {
	        console.log('Cancel');
	      },
	    });
	  
   /* console.log('=================')
    console.log(record)
    this.props.history.push({
      pathname: '/Addgoodlist',
      state: { record: record }
    }) */
  };
  
  //修改模态框的input触发事件修改商品的数量
  changeGoodsNum = (e) => {
	  console.log(e.target.value);
	  this.setState({
		  inputnum:e.target.value
	  })
	  };
  // 
  // addOrderMsg =() =>{
	//   this.props.history.push({
	//         pathname: '/Addgoodlist',
  //           render: (<Link to="/index/Add"> </Link>)
	//   })
  // };
  // 删除列表中的项目
  handleDelete = key => {
	   const glist= [];
	    glist.push({threeId:key});
   Axios.post("/category/deleteCategory",
   				glist 				   		  
  ).then((data)=>{
	   console.log(data);
    this.getgoodlist();	  		  
   })	
	console.log(key);   
  };
  //批量删除
  batchDeletion = () => {
    const idList = [];
    //将选中的数据中的id循环到我们自己定义的idList中
    const { selectedRows } = this.state
    selectedRows.forEach(item => {
      const id = item.key
	 
	  
      idList.push({threeId:id})
    })
    console.log(idList);
	Axios.post("/category/deleteCategory",
					   idList 					   		  
	).then((data)=>{
		   console.log(data);
	 this.getgoodlist();	  		  
	})	
		  
  }
  //选择时间范围的表单变化触发的函数
  handleSelectTime = (value, dateString) => {
    console.log('选择的时间：', dateString)
    this.setState({
      startDistribution: dateString[0],
      endDistribution: dateString[1],
    })
  }
  // 多条件搜索的表单
  getFields = () => {
    // const count =  6;
    const children = [];
    // const dateFormat = 'YYYY-MM-DD';
    children.push(
      <Col span={8} key='1'>
        <Form.Item
          name={`searchKeyword`}
          label={`关键字:`}
          rules={[
            {
              required: true,
              message: '请输入搜索关键字!',
            },
          ]}
        >
          <Input size="small" placeholder="模糊搜索" />
        </Form.Item>
        <p id='searchTitle'>输入商品名称 进行搜索</p>

      </Col>,
    
     
    );
    return children;
  };

  // =================================================================
  render() {
	  console.log(this.state.orderData)
    //多条件搜索表单验证  得到表单的数据
    const onFinish = values => {
		Axios.post("/category/findLike",
		{			
			threeName:values.searchKeyword			
		}		
		).then(data=>{
				  console.log(data)
				 const mydata = [];
				  console.log(data.data.data);
				 data.data.data.forEach((val)=>{
					 mydata.push({
					key: val.threeId,
					goodsALLCategory:val.oneName,
					goodsCategory:val.twoName,
					goodsName:val.threeName,
					goodsQuantity: val.categoryNumber
					})
				 }) 
				 console.log(mydata)
				 this.setState({
				   orderData: mydata,
				 }); 
				  
		})		
      values.startDistribution = this.state.endDistribution
      values.endDistribution = this.state.endDistribution
      console.log('Received values of form: ', values);
    };
    const { selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };

    return (
      <div id='allOrderSearch'>
        <div className='orderSearch'>
          <Form
            name="advanced_search"
            className="ant-advanced-search-form"
            onFinish={onFinish}
          >
            <Row gutter={24}>{this.getFields()}</Row>
            <Row>
              <Col
                span={24}
                style={{
                  textAlign: 'right',
                }}
              >
                <Space size='large'>
                  <Button type="primary" htmlType="submit">搜索</Button>
                  <Button onClick={this.getgoodlist}>重置</Button>
                </Space>

              </Col>
            </Row>
          </Form>
        </div>
        <div className='orderList'>
			
          <div className='btns'>
            <Popconfirm okText="删除" cancelText="取消" title="确定删除吗?" onConfirm={this.batchDeletion}>
              <Button size="small" >批量删除</Button>
            </Popconfirm>
             <Link to={"/index/Add"}>
                 <Button size="small" >新增</Button>
             </Link>

          </div>
          <div>
            <Table size='small' rowSelection={rowSelection} columns={this.state.columns} dataSource={this.state.orderData} />
          </div>
        </div>
      </div>
    )
  }
}

export default OrderManagement