import React from "react";
import PropTypes from "prop-types";
import { Spin, Input } from "antd";
import axios from "axios";
import $script from "scriptjs";
import styles from "./AMap.less";

const googleMapSdk = "https://maps.googleapis.com/maps/api/js?key=your key";
const gaodeMapSdk =
  "https://webapi.amap.com/maps?v=1.4.2&key=4c926e28ae5bda7c37b2f8d3ba18b96d";

let map = null;
let marker = null;
let geocoder = null;
let zoomLevel = 15;

class AAA extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      status: 0,
      jwd: 0,
    };
  }

  componentWillMount() {
    if (!window.AMap && !(window.google && window.google.maps)) {
      axios
        .get(googleMapSdk, { timeout: 1000 })
        .then((res) => {
          $script([googleMapSdk], function (a, b) {});
        })
        .catch(function (error) {
          $script([gaodeMapSdk], function (a, b) {});
        });
    }
  }
  componentDidMount() {
    let _this = this;
    let JWD = "";
    function listenerStorage() {
      if (window.AMap || (window.google && window.google.maps)) {
        if (window.AMap) {
          const { lat, lng, getMapAddress } = _this.props;
          const latlngxy = [
            !lng || lng === "undefined" || lng === "0" ? 104.06574 : lng,
            !lat || lat === "undefined" || lat === "0" ? 30.657469 : lat,
          ]; //默认天府广场
          map = new window.AMap.Map("allmap", {
            resizeEnable: true,
            center: latlngxy,
            zoom: zoomLevel,
          });

          //高德设置语言    ['en', 'zh_en', 'zh_cn']
          let mapLang;
          if (window.localStorage.getItem("i18n") === "en_US") {
            mapLang = "en";
          } else {
            mapLang = "zh_cn";
          }
          map.setLang(mapLang);

          // 在新中心点添加 marker
          marker = new window.AMap.Marker({
            map: map,
            position: latlngxy,
          });

          //为地图注册click事件获取鼠标点击出的经纬度坐标=======================
          // var map = new window.AMap.Map("container", {
          //   resizeEnable: true,
          // });
          // let that = this;
          map.on("click", function (e) {
            JWD = document.getElementById("lnglat").value =
              e.lnglat.getLng() + "," + e.lnglat.getLat();
            console.log(e.lnglat);
            console.log("获取到经纬度：", JWD);
            _this.setState({
              jwd: JWD,
            });
          });

          //==================================================================
          map.on("click", function (e) {
            marker.setPosition(e.lnglat);
            // document.getElementById("lnglat").value =
            //   e.lnglat.getLng() + "," + e.lnglat.getLat();
            window.AMap.service("AMap.Geocoder", function () {
              //回调函数

              //实例化Geocoder
              geocoder = new window.AMap.Geocoder({});
              geocoder.getAddress(e.lnglat, function (status, result) {
                if (status === "complete" && result.info === "OK") {
                  const address = result.regeocode.formattedAddress;
                  getMapAddress && getMapAddress(address);
                }
              });
            });
          });
        }

        _this.setState({
          status: 1,
        });
      } else {
        setTimeout(function () {
          listenerStorage();
        }, 800);
      }
    }
    listenerStorage();
  }

  onChange = (e) => {
    console.log("点击了经纬度", this.JWD);
  };
  componentWillReceiveProps = (nextProps) => {
    const { getMapPoint } = this.props;
    if (
      window.AMap &&
      nextProps.address &&
      nextProps.address !== this.props.address
    ) {
      window.AMap.service("AMap.Geocoder", function () {
        //回调函数
        //实例化Geocoder
        geocoder = new window.AMap.Geocoder({});
        geocoder.getLocation(nextProps.address, function (status, result) {
          if (status === "complete" && result.info === "OK") {
            let latlng = result.geocodes[0].location;
            getMapPoint && getMapPoint(latlng);
            // 设置缩放级别和中心点
            let latlngxy = [latlng["lng"], latlng["lat"]];
            const currentZoom = map.getZoom();
            map.setZoomAndCenter(
              currentZoom !== zoomLevel ? currentZoom : zoomLevel,
              latlngxy
            );
            // 在新中心点添加 marker
            marker.setPosition(latlng);
          } else {
            console.log('search "' + nextProps.address + '" no data');
          }
        });
      });
    }
  };
  CF() {
    console.log(this.state.jwd);
    console.log(this.props.sonMess(this.state.jwd));
  }
  render() {
    const { height } = this.props;

    return (
      <div
        className={styles.container}
        style={{ height: height ? height : 300 }}
      >
        {/* <ID /> */}
        <h2>经纬度（粘贴复制到地图定位即可）:{this.state.jwd}</h2>
        <Spin
          spinning={this.state.status === 0 ? true : false}
          tip="Loading..."
        >
          <div
            id="allmap"
            className={styles.mapContainer}
            style={{ height: height ? height : 800 }}
          />

          <div id="container" className="map"></div>
          <div className="input-card" style={{ opacity: 0 }}>
            <h4 onClick={this.CF.bind(this)}>
              请再次点击我获取经纬度
              {/* {this.props.mess},{this.props.sonMess(this.state.jwd)} */}
            </h4>
            <div className="input-item">
              <Input
                type="text"
                readOnly={false}
                id="lnglat"
                // defaultValue={this.props.sonMess(this.state.jwd)}
              />
            </div>
          </div>
        </Spin>
      </div>
    );
  }
}

AAA.propTypes = {
  lng: PropTypes.string,
  lat: PropTypes.string,
  className: PropTypes.string,
};

export default AAA;
