import React, { Component } from 'react'
import { Input, Button, Table,Popconfirm,message } from 'antd'

import Axios from '../../../util/Axios'


import '../../../css/ZhanDianHouTai/Distribute.css'

const { Search } = Input;
class Distribute extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedRowKeys: '',
      selectedRows: '',
      Distributors: '',
      columns: [
        { title: '姓名', dataIndex: 'distributorName', className: 'red', align: 'center' },
        { title: '电话', dataIndex: 'distributorPhone', align: 'center' },
        { title: '当前接单数', dataIndex: 'haveOrderNum', align: 'center' },
        { title: '距离站点', dataIndex: 'distance', align: 'center' },
        { title: '交通工具', dataIndex: 'vehicle', align: 'center' },
      ]
    }
  }

  componentDidMount() {
    console.log(this.props.location.state);
    Axios.get('/marki/select')
      .then(res => {
        console.log(res);
        this.getDistributors(res.data)
      })
  }
  //模拟axios请求的到订单列表的数据
  getDistributors = (val) => {
    console.log(val);
    let myData = [];
    val.data.forEach(item => {
      let car = item.van === '可用' ? '面包车' : '电动车';
      // console.log(car);
      myData.push({
        key: item.staffId,
        distributorName: item.staffName,
        distributorPhone: item.staffPhoneNumber,
        haveOrderNum: item.currentOrders,
        distance: item.stationDistance,
        vehicle: car,
      })
    })
    //2.修改State
    this.setState({
      Distributors: myData,
    });
  }
  //表格行的单选
  onSelectChange = (selectedRowKeys, selectedRows) => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    console.log(selectedRows);
    this.setState({ selectedRowKeys, selectedRows });
  };
  //确认选择某个配送员
  chooseSure = () => {
    console.log(this.state.selectedRows)
  };
  //返回上一页
  goBackPage = () => {
    window.history.back(-1)
  };

  //模糊查询
  queryStaff = val => {
    console.log(val);
    Axios.post('/marki/vagueFind', {
      // params: {
      vagueFind: val
      // }
    })
      .then(res => {
        console.log(res);
        this.getDistributors(res.data)
      })
  }

  //平台发货
  platformDelivery = () =>{
    let that = this;
    if(this.state.selectedRows.length > 0){
      console.log(this.state.selectedRows[0].key);
      console.log(this.props.location.state.orderid);
      Axios.post('/marki/orderInfo',{
        orderId:this.props.location.state.orderid,
        courierId:this.state.selectedRows[0].key
      }).then(res => {
        console.log(res);
        that.props.history.push({
          pathname: '/index/DispatchDetails',
          state: { DetailsMsg: res.data.data }
        })
      })
    }else{
      message.error('请选择您要派送的订单！！！');
    }
  };
  render() {
    const { selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <div id='distribute'>
        <div id='goBack'>
          <Button size="small" onClick={this.goBackPage}>返回</Button>
        </div>
        <div id='searchBox'>
          <Search placeholder="通过配送员姓名、电话" style={{ width: 300 }} onSearch={this.queryStaff} />
        </div>
        <div id='chooseP'>
          <Popconfirm okText="是" cancelText="否" title="是否配送给当前配送员?" onConfirm={this.platformDelivery}>
            <Button size='large'>平台发货</Button>
          </Popconfirm>

        </div>
        <div>
          <Table
            locale={{ emptyText: '暂无数据' }}
            rowSelection={{
              type: 'radio',
              ...rowSelection
            }} columns={this.state.columns} dataSource={this.state.Distributors} bordered />
        </div>
      </div>
    )
  }
}

export default Distribute