import React, {Component} from 'react'
import "../css/wmx-css/wmx_Additiion.css"
import Axios from "../util/Axios";
import {Input, Button} from "antd"
import {Link} from "react-router-dom";

class Addition extends Component {
        constructor(props) {
            super(props);
            this.input_myname = React.createRef();
            this.input_unitPrice =React.createRef();
            this.input_unit =React.createRef();
            this.input_unitCost =React.createRef();
            this.input_supCost =React.createRef();
            this.input_siteName =React.createRef();
                this.state={
                    supName:'',
                    unitPrice:'',
                    unit:'',
                    unitCost:'',
                    supCost:'',
                    siteName:''
            }
        }
     // UNSAFE_componentWillMount() {
     //
     // }

    CallBack =() =>{
        console.log("========");
        this.props.history.push({
            pathname:'/index/MaterialManagement'
        })
    };
    //    保存数据
    Save =() =>{
        console.log("=======保存");
        console.log(this.input_myname.current.state.value);
        console.log(this.input_unitPrice.current.state.value);
        console.log(this.input_unit.current.state.value);
        console.log(this.input_unitCost.current.state.value);
        console.log(this.input_supCost.current.state.value);
        console.log(this.input_siteName.current.state.value);
        Axios.put("/materialManagement",{
                supName: this.input_myname.current.state.value,
                supUnitPrice: this.input_unitPrice.current.state.value,
                unit: this.input_unit.current.state.value,
                unitCost: this.input_unitCost.current.state.value,
                supCount: this.input_supCost.current.state.value,
                siteName: this.input_siteName.current.state.value
            }).then((data) =>{
                console.log(data);
        }).catch((err) =>{
            console.log(err)
        })
    };
    render() {
        return (
            <div>
                <div className="callback">
                    <Link to={"/index/m"}>
                        <Button value="default" onClick={() =>this.CallBack()}>返回</Button>
                    </Link>
                </div>
                <div className="mar">
                    <label>物料名称：</label>
                    <Input style={{width:300}} defaultValue={this.state.supName} ref={this.input_myname}/>
                </div>
                <div className="mar">
                    <label>物料单价：</label>
                    <Input style={{width:300}} defaultValue={this.state.unitPrice} ref={this.input_unitPrice}/>
                </div>
                <div className="mar">
                    <label>物料单位：</label>
                    <Input style={{width:300}} defaultValue={this.state.unit} ref={this.input_unit}/>
                </div>

                <div className="mar">
                    <label>物料成本：</label>
                    <Input style={{width:300}} defaultValue={this.state.unitCost} ref={this.input_unitCost}/>
                </div>
                <div className="mar">
                    <label>物料数量：</label>
                    <Input style={{width:300}} defaultValue={this.state.supCost} ref={this.input_supCost}/>
                </div>
                <div className="mar">
                    <label>物料站点：</label>
                    <Input style={{width:300}} defaultValue={this.state.siteName} ref={this.input_siteName}/>
                </div>
                <div className="bao">
                    <Button value="default" onClick={() =>this.Save()}>保存</Button>
                </div>
            </div>

         )
    }
}

export default Addition