import React, { Component } from 'react';
import { Link } from "react-router-dom";
import'../../css/thr/goodlist1.css';
import {
	Input,
	// Select,
	Button,
	// Space,
	// Table,
	Form,
	Row,
	Col,
	// Switch
} from 'antd';

import Axios from"../../util/Axios.js";
// const { Option, OptGroup } = Select;
// function handleChange(value) {
//   console.log(` ${value}`);
// }
class Addgoodlist extends Component {
	
	/* search(){
			const inpVal = this.input.value;
			console.log(inpVal);
		} */
	render() {
	/* 	function handleChange(value) {
		  console.log(`${value}`);
		} */
		const onFinish = values => {
			Axios.post("/category",{
				oneName:values.goodsalltype,
			    twoName:values.goodstype,
				categoryName:values.goodsname,
				categoryNumber:values.goodnum
							  					   		  
			}).then((data)=>{
				  		  console.log(data);
			});
			setTimeout(()=>{
				this.props.history.push({
				  pathname: "/index/lei"
				});
			},1000);		
				console.log('Received values of form: ', values);
			};
       /* const handsleChange =()=>{
			
		} */
        return (
	        <div >
			<div className='Addgoodlist-top'>
	           <Link to="/index/lei">
	            <button >返回</button>
	            </Link>
	        </div>
			<Form    
			 name="advanced_search"
			 className="ant-advanced-search-form"
			 onFinish={onFinish}
			                        
			 >
			 
			 <Row>
			 		        <Col span={5} key='1'>
			 		    <Form.Item
			 		        name={`goodsalltype`}
			 		        label={`商品大类:`}
			 		        rules={[
			 		            {
			 		                required: true,
			 		                message: '请输入商品大类!',
			 		            },
			 		        ]}
			 		    >
			 <
			 		Input size = "small"
			 		placeholder = "请输入商品大类"   />
			 		       
			 		    </Form.Item>												
			 		</Col>
			 		        </Row>
			 
			 
			 
			 
			 
			 <Row>
			 		        <Col span={5} key='1'>
			 		    <Form.Item
			 		        name={`goodstype`}
			 		        label={`商品细类:`}
			 		        rules={[
			 		            {
			 		                required: true,
			 		                message: '请输入商品细类!',
			 		            },
			 		        ]}
			 		    >
			 <
			 		Input size = "small"
			 		placeholder = "请输入商品细类"   />
			 		       
			 		    </Form.Item>												
			 		</Col>
			 		        </Row>
			 
			 
			 
			 
			 
			                        <Row>
			                        		        <Col span={5} key='1'>
			                        		    <Form.Item
			                        		        name={`goodsname`}
			                        		        label={`商品名称:`}
			                        		        rules={[
			                        		            {
			                        		                required: true,
			                        		                message: '请输入商品名称!',
			                        		            },
			                        		        ]}
			                        		    >
			                        <
			                        		Input size = "small"
			                        		placeholder = "请输入商品名称"   />
			                        		       
			                        		    </Form.Item>												
			                        		</Col>
			                        		        </Row>
			            
									<Row>
									        <Col span={5} key='1'>
									    <Form.Item
									        name={`goodnum`}
									        label={`商品数量:`}
									        rules={[
									            {
									                required: true,
									                message: '请输入商品数量!',
									            },
									        ]}
									    >
							<
									Input size = "small"
									placeholder = "请输入商品数量"   />
									       
									    </Form.Item>												
									</Col>
									        </Row>
								
			                        <Row>
			                            <Col
			                                span={5}
			                                style={{
			                                    textAlign: 'right',
			                                }}
			                            >
			                                <Button type="primary" htmlType="submit" >
			                                    保存
			                                </Button>
			                            </Col>
			                        </Row>
			                    </Form>
			 </div>
        );
    }
}
export default Addgoodlist;
