import React, {Component} from 'react'
import {Button, Divider, Typography, } from "antd";
import '../../css/ZhanDianHouTai/Reviewsure.css'
import Axios from '../../util/Axios'


const {Title} = Typography;
class Reviewd_sure extends Component {
    constructor(props){
        super(props);
        this.state={
            checkStatus:'',
            orderId:'',
            consigneeAddress:'',
            consigneeName:'',
            consigneePhoneNumber:'',
            street:'',
            userName:'',
            cashNumber:'',
            serialNumber:'',
            streamTime:'',
            display:''
        }
    }
    componentDidMount() {
        console.log(this.props.location.query);
        let item={checkStatus:this.props.location.query.checkStatus,orderId:this.props.location.query.orderId};
        Axios.post("/findDeliveryDetails",item).then(data=>{
            console.log("======================");
            console.log(data.data.data[0]);
            let item=data.data.data[0];
            if (item.checkStatus==='审核通过'){
                this.setState({
                    checkStatus:item.checkStatus,
                    orderId:item.orderId,
                    consigneeAddress:item.consigneeAddress,
                    consigneeName:item.consigneeName,
                    consigneePhoneNumber:item.consigneePhoneNumber,
                    street:item.street,
                    userName:item.userName,
                    cashNumber:item.delivery[0].cashNumber,
                    serialNumber:item.delivery[0].serialNumber,
                    streamTime:item.delivery[0].streamTime,
                    display:'block'
                })
            } else {
                this.setState({
                    checkStatus:item.checkStatus,
                    orderId:item.orderId,
                    consigneeAddress:item.consigneeAddress,
                    consigneeName:item.consigneeName,
                    consigneePhoneNumber:item.consigneePhoneNumber,
                    street:item.street,
                    userName:item.userName,
                    cashNumber:item.delivery[0].cashNumber,
                    serialNumber:item.delivery[0].serialNumber,
                    streamTime:item.delivery[0].streamTime,
                    display:'none'
                })
            }

        })
    }

    goBackPage=()=>{
        this.props.history.push({
            pathname:"/index/audit"
        })
    };
    render() {
        return (
            <div id="reviewed_sure">
                <div className="sure_top">
                    <Button size="small" onClick={this.goBackPage}>返回</Button>
                    {/*<div><p>订单状态：</p><Button size="small" style={{display:this.state.display,float:"right",marginLeft:10}}>发货</Button></div>*/}

                </div>
                <Divider/>
                <div className="sure_middle1">
                    <div>
                        <p>订单编号：{this.state.orderId}</p>
                        <p>所属街道：{this.state.street}</p>
                    </div>
                    <div>
                        <p>
                            用户：{this.state.userName}
                        </p>
                    </div>
                    <div>
                        <p>收货人：{this.state.consigneeName}</p>
                    </div>
                    <div>
                        <p>电话：{this.state.consigneePhoneNumber}</p>
                    </div>
                    <div>
                        <p>收货地址：{this.state.consigneeAddress}</p>
                    </div>
                </div>
                <Divider/>
                <div className="sure_middle2">
                    <Title level={4}>购物凭条：</Title>
                    <div>
                        <p>收银条台号：</p>
                        <p>{this.state.cashNumber}</p>
                    </div>
                    <div>
                        <p>流水号：</p>
                        <p>{this.state.serialNumber}</p>
                    </div>
                    <div>
                        <p>日期：</p>
                        <p>{this.state.streamTime}</p>
                    </div>
                </div>
                <Divider/>
                <div className="sure_bottom">
                    <p>审核情况：</p>
                    <p>{this.state.checkStatus}</p>
                </div>
            </div>
        )
    }
}

export default Reviewd_sure
