import React, {Component} from 'react'
import "../css/wmx-css/wmx_DeliverySet.css"
import {Input ,Button} from 'antd'
import Axios from "../util/Axios";
class DeliverySetment extends Component {
    constructor(props) {
        super(props);

        this.input_setTime =React.createRef();
        this.state ={
            setTime:'',
        }
    }

    SaveTime= () =>{
        console.log("保存时间=====");
        console.log(this.input_setTime.current.state.value);

    //     发送请求来保存时间
        Axios.post("/basicSettings/setTime",
            {setTime:this.input_setTime.current.state.value})
            .then((data) =>{
                console.log(data)
            }).catch((err) =>{
            console.log(err +" b保存失败")
        })

    };
    render() {
        return (
            <div>
                <div className="pro">
                    <label>设置配送员接单时间:</label>
                    <Input style={{ width: 300 }} ref={this.input_setTime}/>
                    <span>文本标签</span>
                </div>

                <Button className="Btn" onClick={() =>this.SaveTime()}>保存</Button>
            </div>
        )
    }
}

export default DeliverySetment