import {observable,action} from 'mobx'

// import Axios from 'axios';
import Axios from "../util/Axios";
import Api from "../api/index"
export default class UserStore {
    @observable user =[];
    @observable isLogin =false;
    @observable token = "";

    @action
    login = (values) =>{
        return new Promise((resolve,reject)=>{
            Axios.post(Api.user.userLogin,{staffNumber:values.username,staffPassword:values.password})
                .then((res) => {
                    console.log(res);
                    if(res.status === 200){
                        this.user = res.data.data.roleName;
                         const  token = res.data.data.Authorization;

                        console.log(this.user);
                        console.log(res.data.data.roleName);
                        console.log(this.token);
                        sessionStorage.setItem('token',token);
                        resolve(res.data.data.roleName);

                    }else {
                        reject("登录失败！" ,res);
                    }
                })
        })
    }
}