import React, {Component} from 'react'
import Axios from '../../util/Axios'
import { Input } from 'antd';
// import { AudioOutlined } from '@ant-design/icons';
import { Radio } from 'antd';
import { Table } from 'antd';
import {Space } from 'antd';
import { Modal,Select} from 'antd';
// const { Column, ColumnGroup } = Table;
import { ExclamationCircleOutlined } from '@ant-design/icons';
import '../../css/LiKeliang/Operator.css'
// const { Search } = Input;
const { confirm } = Modal;
const { Option } = Select;
//=================================input框========================================
// const suffix = (
//     <AudioOutlined
//         style={{
//             fontSize: 16,
//             color: '#1890ff',
//         }}
//     />
// );
let myNum=1;
class Operator extends Component {
    constructor(){
        super();
        this.input_myLogin=React.createRef();
        this.input_myName=React.createRef();
        // this.input_myRole=React.createRef();
        this.input_myPwd=React.createRef();
        this.searchOne=React.createRef();
        this.state={
            selectedRowKeys: [], // Check here to configure the default column
            visible: false,
            oldData:[],
            data:'',
            myLogin:'',
            myName:'',
            myPwd:'',
            roleName:'',
            roleId:'',
            destroyOnClose:true,
            input_key:'',
            add:'add',
            disabled:true,
            role:[],
            tankuang:''

        }
    }




    //修改模态框相关设置============================
    showModal = (mykey) => {
        // console.log(mykey);
        if(mykey==='add'){
            // console.log(12345);
            this.setState({
                visible: true,
                myLogin:'',
                myName:'',
                myPwd:'',
                roleName:'',
                roleId:'',
                input_key:mykey,
                destroyOnClose:false,
                disabled:false,
                tankuang:'新增弹框'
            })
        }
        // console.log(this.state.data[mykey]);
        else {
            this.setState({
                visible: true,
                myLogin:this.state.data[mykey].staffNumber,
                myName:this.state.data[mykey].staffName,
                roleName:this.state.data[mykey].roleName,
                myPwd:this.state.data[mykey].staffPassword,
                input_key:mykey,
                roleId:this.state.data[mykey].roleId,
                staffId:this.state.data[mykey].staffId,
                destroyOnClose:false,
                tankuang:'编辑弹框'
            });
        }

    };
    //=================================角色职位选择
     handleChange=(value,title)=> {
        console.log(title);
        this.setState({
            roleId:title.value,
            roleName:title.title
        })
    };

    handleOk = e => {
        if (this.state.input_key==='add'){

            // console.log('新增员工');
            // console.log(this.state.data.length);
            let inputK=this.state.data.length;
            let inputL=this.input_myLogin.current.state.value;
            let inputN=this.input_myName.current.state.value;
            // let inputR=this.input_myRole.current.state.value;
            let inputP=this.input_myPwd.current.state.value;
            let items=[...this.state.data];
            items.push({key:inputK,staffNumber:inputL,staffName:inputN,roleName:this.state.roleName,roleId:this.state.roleId,operation:'',staffPassword:inputP});
            let item={staffNumber:inputL,staffName:inputN,roleName:this.state.roleName,staffPassword:inputP,roleId:this.state.roleId,staffId:'',addTime:''};
            console.log(item);
            Axios.post("/operator/insertStaffRole",item).then(data=>{
                console.log(data)
            });
            this.setState({
                visible:false,
                disabled:true,
                destroyOnClose:true,
                data:items
            },()=>{
                // console.log('新增成功')
            })
        } else {
            // console.log('修改员工信息');
            // console.log(this.state.roleName);
            // console.log(this.input_myLogin.current.state.value);
            let inputK=this.state.input_key;
            let inputL=this.input_myLogin.current.state.value;
            let inputN=this.input_myName.current.state.value;
            let inputP=this.input_myPwd.current.state.value;
            let items = [...this.state.data];
            items[this.state.input_key]={key:inputK,staffNumber:inputL,staffName:inputN,roleName:this.state.roleName,operation:'',staffPassword:inputP,
                addTime:items[this.state.input_key].addTime,roleId:this.state.roleId,staffId:items[this.state.input_key].staffId};
            // console.log(items);
            // console.log(this.state.data);
            let myitem={staffNumber:inputL,staffName:inputN,staffPassword:inputP,roleName:this.state.roleName,addTime:items[this.state.input_key].addTime,
                roleId:this.state.roleId,staffId:items[this.state.input_key].staffId};
            // let myitem={staffNumber:inputL,staffName:inputN,staffPassword:inputP,roleName:inputR};
            console.log(myitem);
            this.setState({
                visible: false,
                data:items,
                destroyOnClose:true,
            },() => {
                // console.log('触发回调函数');
                Axios.put("/operator/updateStaffRole",myitem).then(data=>{
                    // console.log(data)
                })
            },()=>{
                // console.log('修改成功') ;

            });
        }

        // console.log(this.state.data);
        setTimeout(this.clearContent(),2000)
    };
    //============清除弹框内容
    clearContent(){
        this.setState({
            destroyOnClose:true
        })
    }
    handleCancel = e => {
        // console.log(e);
        this.setState({
            visible: false,
            destroyOnClose:true,
            disabled:true
        });
    };
//=============================重置密码=================================
    changePwd=(mykey)=>{
        let items=[...this.state.data];
        // items[mykey].staffPassword='123456';
        let item={staffId:items[mykey].staffId};
        // console.log(item);
        // console.log(items);
        Axios.put("/operator/updatePassword",item).then(data=>{
            // console.log(data)
        });
        this.setState({
            data:items,
        })
    };




//======================删除弹框====================================
    showConfirm(mykey) {
        let that=this;
        this.setState({
            input_key:mykey
        });
        confirm({
            title: '确认要删除该条数据?',
            icon: <ExclamationCircleOutlined />,
            content: '删除后将不可恢复',
            onOk() {
                // console.log(mykey);
                let items=[...that.state.data];
                let myDelete=[];
                for (let i=0;i<mykey.length;i++){
                     myDelete.push({staffId:items[items.findIndex(item=>item.key===mykey[i])].staffId,roleId:items[items.findIndex(item=>item.key===mykey[i])].roleId,
                         statusCode:1});
                    items.splice(items.findIndex(item => item.key === mykey[i]), 1);
                }
                console.log(myDelete);
                that.setState({
                    data:items
                },()=>{
                    Axios.put("/operator/deleteStaffRole",myDelete).then(res=>{
                        // console.log(res)
                    })
                })
            },
            onCancel() {
                // console.log('Cancel');
            },
        });
    }
//============================模糊查询========================================
mySearch=()=>{
        let vague=this.searchOne.current.state.value;
        let roleName=this.state.roleName;
        console.log({vague,roleName});
    if (vague!=undefined&&roleName!=''){
        Axios.post("/operator/operator",{vague,roleName}).then(res=>{
            // console.log(res);
            this.copyData(res.data.data)
        })
    }

};

//======================================封装渲染页面数据的函数===============================
    copyData=(data)=>{
        let myData=[];
        console.log(myNum);
        myNum++;
        // console.log(this.state.oldData);
        for (let i = 0; i < data.length; i++) {
            myData.push({
                key: i,
                staffNumber:data[i].staffNumber,
                staffName: data[i].staffName,
                roleName: data[i].roleName,
                operation:'',
                staffPassword:data[i].staffPassword,
                addTime:data[i].addTime,
                roleId:data[i].roleId,
                staffId:data[i].staffId

            });
            // console.log(res.data.data[i].staffName)

        }
        if (myNum===2){
            this.setState({
                data:myData,
                oldData:myData
            })
        } else {
            this.setState({
                data:myData,
            })
        }

    };

//===================批量删除=================================
    onSelectChange = selectedRowKeys => {
        // console.log('selectedRowKeys changed: ', selectedRowKeys);
        this.setState({ selectedRowKeys });
    };
    componentDidMount() {
        let myRoleName=[];
        Axios.get("/operator/operatorAll").then((res)=>{
            // console.log(res.data.data);
            this.copyData(res.data.data)
        });
        Axios.get("/role").then(res=>{
            console.log(res.data.data);
            for (let i=0;i<res.data.data.length;i++){
                myRoleName.push({
                    roleId:res.data.data[i].roleId,
                    roleName:res.data.data[i].roleName
                })
            }
            this.setState({
                role:myRoleName
            })
            // console.log(this.state.role)
        })



        // console.log(myData)
    }
    componentWillUpdate(nextProps, nextState, nextContext) {

    }


    render() {
            // console.log("触发render");
        // let mydata =[...this.state.data];
//====================表格里的数据在这里设置==================
//         const data = [];
        let myRole=[...this.state.role];
        let roleArr=myRole.map((item,index)=>{
            return(
                <Option value={item.roleId} key={index} title={item.roleName}>{item.roleName}</Option>
            )
        });
        //====================================表格======================================
        const columns = [
            {
                title: '登录账号',
                dataIndex: 'staffNumber',
            },
            {
                title: '真实姓名',
                dataIndex: 'staffName',
            },
            {
                title: '所属角色',
                dataIndex: 'roleName',
            },
            {
                title: '操作',
                dataIndex: 'operation',
                render:(text, record) => (
                    //这里可以绑定key值来设置路由跳转

                    <Space size="middle">
                        <span onClick={()=>{this.changePwd(record.key)}}>重置密码</span>
                        <span  onClick={()=>this.showModal(record.key)}>编辑</span>
                        <span onClick={()=>this.showConfirm([record.key])}>删除</span>
                    </Space>
                )
            }

        ];

        //============================表格==================================
        const { selectedRowKeys } = this.state;
        const rowSelection = {
            selectedRowKeys,
            onChange: this.onSelectChange,
            selections: [
                Table.SELECTION_ALL,
                Table.SELECTION_INVERT,
                {
                    key: 'odd',
                    text: 'Select Odd Row',
                    onSelect: changableRowKeys => {
                        let newSelectedRowKeys = [];
                        newSelectedRowKeys = changableRowKeys.filter((key, index) => {
                            if (index % 2 !== 0) {
                                return false;
                            }
                            return true;
                        });
                        this.setState({ selectedRowKeys: newSelectedRowKeys });
                    },
                },
                {
                    key: 'even',
                    text: 'Select Even Row',
                    onSelect: changableRowKeys => {
                        let newSelectedRowKeys = [];
                        newSelectedRowKeys = changableRowKeys.filter((key, index) => {
                            if (index % 2 !== 0) {
                                return true;
                            }
                            return false;
                        });
                        this.setState({ selectedRowKeys: newSelectedRowKeys });
                    },
                },
            ],
        };
        return (
            <div id='Operator_main'>
                <div className='Operator_top'>
                    <label>模糊查询：</label>
                    <Input
                        placeholder="请输入模糊查询内容"
                        // onSearch={value => console.log(value)}
                        style={{ width: 200 ,marginRight:10}}
                        ref={this.searchOne}
                    />
                    <label>所属角色：</label>
                    {/*<Input*/}
                    {/*    placeholder="input search text"*/}
                    {/*    // onSearch={this.mySearch}*/}
                    {/*    style={{ width: 200 ,marginRight:40}}*/}
                    {/*    ref={this.searchTwo}*/}
                    {/*/>*/}
                    <Select defaultValue={this.state.roleName} style={{ width: 120 }} onChange={this.handleChange}>
                        {roleArr}
                    </Select>
                    <Radio.Button value="default" onClick={()=>{this.mySearch()}}>查询</Radio.Button>
                    <Radio.Button value="default" onClick={()=>{this.copyData(this.state.oldData)}}>复位</Radio.Button>
                    <Radio.Button value="default" onClick={()=>{this.showConfirm(this.state.selectedRowKeys)}} style={{ marginRight:20}}>批量删除</Radio.Button>
                    <Radio.Button value="default" onClick={()=>{this.showModal(this.state.add)}}>新增</Radio.Button>
                </div>

                <div>
                    <Table  rowSelection={rowSelection} columns={columns} dataSource={this.state.data} />
                </div>

                {/*{=============================修改弹框}*/}
                <div className="myModal">
                    <Modal
                        title={this.state.tankuang}
                        visible={this.state.visible}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                        destroyOnClose={this.state.destroyOnClose}
                    >
                        <label>登录账号：</label>
                        <Input defaultValue={this.state.myLogin}  ref={this.input_myLogin}/>
                        <label>真实姓名：</label>
                        <Input defaultValue={this.state.myName} ref={this.input_myName}/>
                        <label>所属角色：</label>
                        {/*<Input defaultValue={this.state.myRole} ref={this.input_myRole} />*/}
                        <Select defaultValue={this.state.roleName} style={{ width: 120 }} onChange={this.handleChange}>
                            {roleArr}
                        </Select>
                        <br/>
                        <label>登录密码：</label>
                        <Input defaultValue={this.state.myPwd} ref={this.input_myPwd} disabled={this.state.disabled} type={"password"}/>
                    </Modal>
                </div>
            {/*    新增弹框*/}

            </div>
        );
    }
}

export default Operator
