import React, { Component } from 'react'
// import { AudioOutlined } from '@ant-design/icons';
import { Radio } from 'antd';
import { Table } from 'antd';
import { Space } from 'antd';
import { DatePicker, Form, Input, Select, Modal, Button } from 'antd';
// const { Column, ColumnGroup } = Table;
import { ExclamationCircleOutlined } from '@ant-design/icons';
import 'antd/dist/antd.css';
import '../../css/Zhao/DistributionTop.css';
import Axios from "../../util/Axios.js";

import ExportJsonExcel from "js-export-excel";

import 'moment/locale/zh-cn'; 
import locale from 'antd/es/date-picker/locale/zh_CN';



const { Option } = Select;
// const { Search } = Input;
const { confirm } = Modal;

const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
};
const tailLayout = {
    wrapperCol: { offset: 8, span: 16 },
};


//=================================input框========================================
// const suffix = (
//     <AudioOutlined
//         style={{
//             fontSize: 16,
//             color: '#1890ff',
//         }}
//     />
// );

class DistributionTop extends Component {
    constructor() {
        super();
        this.input_orderId = React.createRef();
        this.input_phone = React.createRef();
        this.input_age = React.createRef();
        this.input_sex = React.createRef();
        this.input_sta = React.createRef();
        this.input_car = React.createRef();
        this.input_mobile = React.createRef();
        this.input_origin = React.createRef();
        this.DeliveryOrigin = React.createRef();
        this.time = React.createRef();
        this.Deliverytime = React.createRef();
        this.state = {
            startTime: '',
            endTime: '',
            // statusCode:'',
            selectedRows: [],
            selectedRowKeys: [], // Check here to configure the default column
            userData: [],
            visible: false,
            data: [],
            // orderId: '',
            phone: '',
            age: '',
            sex: '',
            sta: '',
            car: '',
            mobile: '',
            origin: '',
            DeliveryOrigin: '',
            time: '',
            Deliverytime: '',
            destroyOnClose: true,
            input_key: '',
            add: 'add',
            show:'none',
            block:'none'

        }
    }


    //======================删除弹框====================================
    DelConfirm() {

        // this.setState({
        //     input_key:mykey
        // });
        confirm({
            title: '确认要删除该条数据?',
            okText:'是',
            cancelText:"取消",
            icon: <ExclamationCircleOutlined  />,
            // content: 'Some descriptions',
            
            onOk: () => {
                const arr = [];

                const { selectedRows } = this.state
                console.log(selectedRows)
                selectedRows.forEach(item => {

                    const id = item.orderId
                    arr.push({ orderId: id,statusCode: 1 })

                    // console.log(arr)

                    Axios.put("/auditManagement",
                        arr
                    ).then((response) => {
                        console.log(response);
                        this.getDistribution();
                    })
                        .catch((error) => {
                            console.log(error);
                        });

                })

            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }
    //====================================================================



    //===================查询=================================
    showConfirm =(checkStatus) => {
        console.log(checkStatus);
        this.props.history.push({
            pathname:"/index/dai",
            query:checkStatus,
        })

    };
    ddConfirm =(record) =>{
        console.log(record);
        this.props.history.push({
            pathname:"/index/scuccess",
            query: record,
        })
    };
    //==========================================================

    //==========================模糊查询=======================================
    
    
    onFinish = (values) => {
        console.log(this.state.endTime);
        values.startTime = this.state.startTime;
        values.endTime = this.state.endTime;


        console.log(values);
       

        Axios.post("/auditManagement", values).then(data => {
            console.log(data);
            const mydata = [];
            console.log(data.data.data);
            data.data.data.forEach((vac, index) => {
                mydata.push({
                    orderId: vac.orderId,
                    phone: vac.userName,
                    age: vac.consigneePhoneNumber,
                    sex: vac.consigneeName,
                    sta: vac.startTime,
                    car: vac.street,
                    mobile: vac.consigneeAddress,
                    origin: vac.checkStatus,
                    DeliveryOrigin: vac.orderStatus,
                    time: vac.bornTime,
                    Deliverytime: vac.waitTime,
                    key: index,
                    look:''
                })

            })
            console.log(mydata)

            this.setState({
                data: mydata,
            });
            // this.onFinish()
        });
        console.log(values); // { value: "lucy", key: "lucy", label: "Lucy (101)" }
    }



    // ===================批量删除=================================
    onSelectChange = (selectedRowKeys, selectedRows) => {

        console.log({selectedRows});
        console.log({selectedRowKeys,selectedRows});
        this.setState({ selectedRowKeys, selectedRows });
    };

    //==================================请求用户信息数据==========================================
    componentDidMount() {
        this.getDistribution()

    }
    getDistribution() {
        Axios.get("/auditManagement").then(data => {
            // console.log(data)
            const mydata = [];
            console.log(data.data.data);
            data.data.data.forEach((vac, index) => {
                mydata.push({
                    orderId: vac.orderId,
                    phone: vac.userName,
                    age: vac.consigneePhoneNumber,
                    sex: vac.consigneeName,
                    sta: vac.startTime,
                    car: vac.street,
                    mobile: vac.consigneeAddress,
                    origin: vac.checkStatus,
                    DeliveryOrigin: vac.orderStatus,
                    time: vac.bornTime,
                    Deliverytime: vac.waitTime,
                    key: index,
                    look: ''
                })
            });
            console.log(mydata);
            
            this.setState({
                data: mydata,
            });
          
        })
    }

    

    // =========================死数据=============================================
    // UNSAFE_componentWillMount() {
    //     const myData=[];
    //     for (let i = 0; i < 46; i++) {
    //         myData.push({
    //             key: i,
    //             name: `员工${i+1}`,
    //             phone: `${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}`,
    //             age: `${i+1}`,
    //             sex: `王五. ${i+1}`,
    //             sta:`正常`,
    //             car:`无 `,
    //             mobile:`无 `,
    //             origin:`待审核 `,
    //             DeliveryOrigin:`${i+1}-${i+1}${i+1}-${i+1}`,
    //             time:`${i+1}-${i+1}${i+1}-${i+1}`,
    //             Deliverytime:`${i+1}小时`,
    //             look:''
    //         });
    //     }
    //     this.setState({
    //         data:myData
    //     })
    // }
    // componentWillUpdate(nextProps, nextState, nextContext) {

    // }


    //============================================================



    //=================================导入=========================
    exportExcel = () => {
        const datas = this.state.data ? this.state.data : ""; //表格数据
        var option = {};
        let dataTable = []; //新建数组放数据
        console.log(datas);
        if (datas) {
            for (const data of datas) {
                console.log(data);
                if (data) {
                    let obj = {
                        // key: data.key,
                        // userName: data.userName,
                        // userTel: data.userTel,
                        // latestUse: data.latestUse,
                        // deliveryNo: data.deliveryNo,
                        // site: data.site,
                        // state: data.state,
                        orderId: data.orderId,
                        phone: data.phone,
                        sex: data.sex,
                        age: data.age,
                        car: data.car,
                        mobile: data.mobile,
                        origin: data.origin,
                        DeliveryOrigin: data.DeliveryOrigin,
                        time: data.time,
                        Deliverytime: data.Deliverytime,
                        key: data.key,
                    };
                    dataTable.push(obj);
                }
            }
        }
        console.log(dataTable);
        option.fileName = "会员信息表"; //文件名
        option.datas = [
            {
                sheetData: dataTable, //数据
                sheetName: "会员信息表", //sheet名字 // sheetFilter: [dataTable.id, dataTable.name],//列过滤 // sheetFilter 列过滤(只有在 data 为 object 下起作用)(可有可无)
                sheetHeader: [
                    "订单编号",
                    "用户",
                    "收货人",
                    "联系方式",
                    "所属街道",
                    "收货地址",
                    "状态",
                    "待配送状态",
                    "提交时间",
                    "等待时间",
                    "号数"
                ], //// 第一行
            },
        ];
        var toExcel = new ExportJsonExcel(option);
        toExcel.saveExcel(); //保存
    };
    //===============================================================


    getStartTime = (val, datastring) => {
        console.log(datastring);
        this.setState({
            startTime:datastring
        })
    }
    getEndTime = (val, datastring) => {
        console.log(datastring);
        this.setState({
            endTime:datastring
        })
    }

  

    render() {
        console.log("触发render");
        // console.log(this.state.data);
        // let myData = this.state.data;
        //====================表格里的数据在这里设置==================
        //         const data = [];

        //====================================表格======================================
        const columns = [
            {
                title: '订单编号',
                dataIndex: 'orderId',
            },
            {
                title: '用户',
                dataIndex: 'phone',
            },
            {
                title: '收货人',
                dataIndex: 'sex',
            },
            {
                title: '联系方式',
                dataIndex: 'age',
            },
            {
                title: '所属街道',
                dataIndex: 'car',
            },
            {
                title: '收货地址',
                dataIndex: 'mobile',
            },
            {
                title: '状态',
                dataIndex: 'origin',
            },
            {
                title: '待配送状态',
                dataIndex: 'DeliveryOrigin',
            },
            {
                title: '提交时间',
                dataIndex: 'time',
            },
            {
                title: '等待时间',
                dataIndex: 'Deliverytime',
            },
            {
                title: '号数',
                dataIndex: 'key',
            },
            {
                title: '操作',
                dataIndex: 'look',
                render: (text, record) => (
                    //这里可以绑定key值来设置路由跳转

                    <Space size="middle">
                        <span className='btn'  style={record.origin==='待审核'? {display:'block'}:{display:'none'}} onClick={() => this.showConfirm(record)}>审核</span>
                        <span className='btn1'  style={record.origin==='审核成功'? {display:'block'}:{display:'none'}} onClick={() => this.ddConfirm(record)}>查看</span>
                        <span className='btn2'  style={record.origin==='审核失败'? {display:'block'}:{display:'none'}} onClick={() => this.ddConfirm(record)}>查看</span>
                    </Space>
                )
            }

        ];


        //============================表格==================================
        const { selectedRowKeys } = this.state;
        const rowSelection = {
            selectedRowKeys,
            onChange: this.onSelectChange,
            selections: [
                
                {
                    key: 'odd',
                    text: '基数表格',
                    onSelect: changableRowKeys => {
                        let newSelectedRowKeys = [];
                        newSelectedRowKeys = changableRowKeys.filter((key, index) => {
                            if (index % 2 !== 0) {
                                return false;
                            }
                            return true;
                        });
                        this.setState({ selectedRowKeys: newSelectedRowKeys });
                    },
                },
                {
                    key: 'even',
                    text: '偶数表格',
                    onSelect: changableRowKeys => {
                        let newSelectedRowKeys = [];
                        newSelectedRowKeys = changableRowKeys.filter((key, index) => {
                            if (index % 2 !== 0) {
                                return true;
                            }
                            return false;
                        });
                        this.setState({ selectedRowKeys: newSelectedRowKeys });
                    },
                },
            ],
        };
        return (
            <div>
                <div>
                    <Form
                        {...layout}
                        id="DistributionTop-search"
                        name="DistributionTop-search"
                        onFinish={this.onFinish}
                    >
                        <Form.Item label="关键字：" htmlFor="keywords" name={`vague`}  >
                            <Input id="keywords" placeholder="请输入用户名或电话" />
                        </Form.Item>

                        <Form.Item label="状态：" htmlFor="state" name={`checkStatus`}  style={{width:200}}>
                            <Select id="state">
                                <Option value="待审核">待审核</Option>
                                <Option value="审核成功">审核成功</Option>
                                <Option value="审核失败">审核失败</Option>
                            </Select>
                        </Form.Item>

                        <Form.Item label="提交时间：" htmlFor="ftime" name={`startTime`} className="DistributionleftSearch"style={{width:200}}>
                            <Space direction="vertical">
                                <DatePicker onChange={this.getStartTime} locale={locale}/>
                            </Space>
                        </Form.Item>

                        <Form.Item label="至：" htmlFor="ftime" name={`endTime`} className="DistributionTopSearch" style={{width:200}}>
                            <Space direction="vertical" >
                                <DatePicker onChange={this.getEndTime} locale={locale}/>
                            </Space>
                        </Form.Item>


                        <Form.Item  {...tailLayout}>
                            <Button htmlType="submit" onFinish={this.onFinish} >搜索 </Button>
                        </Form.Item>
                    </Form>
                    <div className='DistributionTopbutton'>
                        <Radio.Button vacue="default" onClick={() => { this.DelConfirm(this.state.selectedRowKeys) }} className="DistributionTopbutton">批量删除</Radio.Button>
                        <Button onClick={this.exportExcel} className="DistributionTopbutton2">导出</Button>
                    </div>
                 

                </div>

                <div>
                    <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.data} size='small' />
                </div>

                {/*{===============审核================}*/}
                {/* <div className="myModal">

                </div> */}


            </div>
        );
    }
}

export default DistributionTop