import React, { Component } from 'react'
// import { AudioOutlined } from '@ant-design/icons';
import { Radio } from 'antd';
import { Table } from 'antd';
import { Space } from 'antd';
import { Form, Input, Select, Modal, message, Button } from 'antd';
// const { Column, ColumnGroup } = Table;
import { ExclamationCircleOutlined } from '@ant-design/icons';
import 'antd/dist/antd.css';
import '../../css/Zhao/Distribution.css';
import Axios from "../../util/Axios.js";

import ExportJsonExcel from "js-export-excel";
// import Search from 'antd/lib/input/Search';

import 'moment/locale/zh-cn';
// import locale from 'antd/es/date-picker/locale/zh_CN';


const { Option } = Select;
// const { Search } = Input;
const { confirm } = Modal;
const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
};
const tailLayout = {
    wrapperCol: { offset: 8, span: 16 },
};

//=================================input框========================================
// const suffix = (
//     <AudioOutlined
//         style={{
//             fontSize: 16,
//             color: '#1890ff',
//         }}
//     />
// );

class Distribution extends Component {
    constructor() {
        super();
        this.input_name = React.createRef();
        this.input_phone = React.createRef();
        this.input_age = React.createRef();
        this.input_sex = React.createRef();
        this.input_sta = React.createRef();
        this.input_car = React.createRef();
        this.input_mobile = React.createRef();
        this.state = {
            freezelist: [],
            selectedRows: [],
            selectedRowKeys: [], // Check here to configure the default column
            userData: [],
            visible: false,
            data: [],
            name: '',
            phone: '',
            age: '',
            sex: '',
            sta: '',
            car: '',
            mobile: '',
            destroyOnClose: true,
            input_key: '',
            add: 'add',
            // disabled:true,
        }
    }

    //修改模态框相关设置============================
    showModal = (mykey) => {
        console.log(mykey);
        if (mykey === 'add') {
            // console.log(12345);
            this.setState({
                visible: true,
                name: '',
                phone: '',
                age: '',
                sex: '',
                sta: '',
                car: '',
                mobile: '',
                input_key: mykey,
                destroyOnClose: false,
                // disabled:false
            })
        }
        // console.log(this.state.data[mykey]);
        else {
            this.setState({
                visible: true,
                name: this.state.data[mykey].name,
                phone: this.state.data[mykey].phone,
                age: this.state.data[mykey].age,
                sex: this.state.data[mykey].sex,
                sta: this.state.data[mykey].sta,
                car: this.state.data[mykey].car,
                mobile: this.state.data[mykey].mobile,
                input_key: mykey,
                destroyOnClose: false
            });
        }

    };

    handleOk = e => {
        if (this.state.input_key === 'add') {

            console.log('新增员工');
            console.log(this.state.data.length);

            let inputK = this.state.data.length;
            let inputL = this.input_name.current.state.value;
            let inputN = this.input_phone.current.state.value;
            let inputC = this.input_age.current.state.value;
            let inputR = this.input_sex.current.state.value;
            let inputg = this.input_sta.current.state.value;
            let inputP = this.input_car.current.state.value;
            let inputf = this.input_mobile.current.state.value;
            let items = [...this.state.data];
            items[this.state.input_key] = {
                key: inputK,
                name: inputL,
                phone: inputN,
                age: inputC,
                sex: inputR,
                sta: inputg,
                operation: '',
                car: inputP,
                mobile: inputf
            };


            let myitem = {

                staffName: inputL,
                staffPhoneNumber: inputN,
                age: inputC,
                sex: inputR,
                freeze: inputg,
                van: inputP,
                batteryCar: inputf,
            };

            this.setState({
                visible: false,
                data: items
            },

                () => {
                    Axios.post("/marki", myitem).then((response) => {
                        console.log(response);
                        this.getDistribution();
                        info()
                    })
                        .catch((error) => {
                            console.log(error);
                        });

                },
            );

            const info = () => {
                message.info('添加成功');
            };
            console.log('触发回调函数')


        } else {
            console.log('修改员工信息');
            // console.log(this.input_name.current.state.value);
            let inputK = this.state.input_key;
            let inputL = this.input_name.current.state.value;
            let inputN = this.input_phone.current.state.value;
            let inputC = this.input_age.current.state.value;
            let inputR = this.input_sex.current.state.value;
            let inputg = this.input_sta.current.state.value;
            let inputP = this.input_car.current.state.value;
            let inputf = this.input_mobile.current.state.value;
            let items = [...this.state.data];

            items[this.state.input_key] = {
                key: inputK,
                name: inputL,
                phone: inputN,
                age: inputC,
                sex: inputR,
                sta: inputg,
                operation: '',
                car: inputP,
                mobile: inputf
            };

            let myitem = {

                staffName: inputL,
                staffPhoneNumber: inputN,
                age: inputC,
                sex: inputR,
                freeze: inputg,
                van: inputP,
                batteryCar: inputf,
                staffId: this.state.data[this.state.input_key].staffId,
            };

            // console.log(items);
            console.log(this.state.data[this.state.input_key]);

            this.setState({
                visible: false,
                data: items,
                destroyOnClose: true,
            },

                () => {
                    Axios.put("/marki", myitem).then((response) => {
                        console.log(response);
                        this.getDistribution();
                        info()
                    })
                        .catch((error) => {
                            console.log(error);
                        });
                },

            );
            const info = () => {
                message.info('修改成功');
            };
            console.log('触发回调函数')
        }

        // console.log(this.state.data);
        setTimeout(this.clearContent(), 2000)
    };
    //============清除弹框内容
    clearContent() {
        this.setState({
            destroyOnClose: true
        })
    }
    handleCancel = e => {
        console.log(e);
        this.setState({
            visible: false,
            destroyOnClose: true
        });
    };
    //=============================重置密码=================================
    // changePwd=(mykey)=>{
    //     let items=[...this.state.data];
    //     items[mykey].pwd='123456';
    //     console.log(items);

    //     this.setState({
    //         data:items,
    //     })
    // };




    //======================删除弹框====================================
    DelConfirm() {

        // this.setState({
        //     input_key:mykey
        // });
        confirm({
            title: '确认要删除该条数据?',
            okText:'确定',
            cancelText:'取消',
            icon: <ExclamationCircleOutlined />,
            // content: 'Some descriptions',

            onOk: () => {
                const arr = [];

                const { selectedRows } = this.state
                console.log(selectedRows)
                selectedRows.forEach(item => {

                    const id = item.staffId
                    arr.push({ staffId: id })

                    console.log(arr)

                    Axios.post("marki/deleteMarki",
                        arr
                    ).then((response) => {
                        console.log(response);
                        this.getDistribution();
                    })
                        .catch((error) => {
                            console.log(error);
                        });

                })

            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }
    //====================================================================

    //======================冻结弹框====================================
    frozenConfirm() {

        confirm({
            title: '确认要执行冻结吗?',
            okText:'确定',
            cancelText:'取消',
            icon: <ExclamationCircleOutlined />,

            onOk: () => {
                const arr1 = [];

                const { selectedRows } = this.state
                console.log(selectedRows)
                selectedRows.forEach(item => {

                    const id = item.staffId
                    arr1.push({ staffId: id })
                })
                // this.setState({
                //     freezelist:arr1
                // })          

                console.log(arr1)

                Axios.post("marki/freeze",
                    arr1
                ).then((response) => {
                    console.log(response);
                    this.getDistribution();
                })
                    .catch((error) => {
                        console.log(error);
                    });
            },
            onCancel() {
                console.log('Cancel');
            },
        });

    }
    //====================================================================

    //======================解冻弹框====================================
    thawConfirm() {

        confirm({
            title: '确认要执行解冻吗?',
            okText:'确定',
            cancelText:'取消',
            icon: <ExclamationCircleOutlined />,

            onOk: () => {
                const arr2 = [];

                const { selectedRows } = this.state
                console.log(selectedRows)
                selectedRows.forEach(item => {

                    const id = item.staffId
                    arr2.push({ staffId: id })
                })
                console.log(arr2)


                Axios.post("marki/notFreeze",
                    arr2
                ).then((response) => {
                    console.log(response);
                    this.getDistribution();
                })
                    .catch((error) => {
                        console.log(error);
                    });


            },
            onCancel() {
                console.log('Cancel');
            },
        });

    }
    //====================================================================

    //==========================模糊查询=======================================
    onFinish = (values) => {
        console.log(values);
        console.log(values.search);

        Axios.post("marki/findLike", {
            staffName: values.search,
            staffPhoneNumber: values.search,
            freeze: values.select
        }).then(data => {
            console.log(data)
            const mydata = [];
            console.log(data.data.data);
            data.data.data.forEach((val, index) => {
                mydata.push({
                    name: val.staffName,
                    phone: val.staffPhoneNumber,
                    age: val.age,
                    sex: val.sex,
                    sta: val.freeze,
                    car: val.van,
                    mobile: val.batteryCar,
                    staffId: val.staffId,
                    key: index,
                    look: ''
                })

            })
            console.log(mydata)

            this.setState({
                data: mydata,
            });
            // this.onFinish()
        });
        console.log(values); // { value: "lucy", key: "lucy", label: "Lucy (101)" }
    }

    // onFinish = (values) => {
    //     console.log("success", values);
    // }



    //=======================================================================

    //===================查询=================================
    showConfirm = (record) => {
        let cgoods = <div>
            <div>序号：{record.staffId}</div>
            <div>名字：{record.name}</div>
            <div>电话：{record.phone}</div>
            <div>年龄：{record.age}</div>
            <div>性别：{record.sex}</div>
            <div>状态：{record.sta}</div>
        </div>

        confirm({
            title: '详细信息',
            okText:'确定',
            cancelText:'取消',
            icon: <ExclamationCircleOutlined />,
            content: cgoods,

            onCancel() {
                console.log('Cancel');
            },
        });
    }
    //==========================================================





    //===================批量删除=================================
    onSelectChange = (selectedRowKeys, selectedRows) => {

        // console.log({selectedRows});
        // console.log({selectedRowKeys,selectedRows});
        this.setState({ selectedRowKeys, selectedRows });
    };


    //==================================请求用户信息数据==========================================
    componentDidMount() {
        this.getDistribution()
    }
    getDistribution() {
        Axios.get("/marki").then(data => {
            console.log(data)
            const mydata = [];
            console.log(data.data.data);
            data.data.data.forEach((val, index) => {
                mydata.push({
                    name: val.staffName,
                    phone: val.staffPhoneNumber,
                    age: val.age,
                    sex: val.sex,
                    sta: val.freeze,
                    car: val.van,
                    mobile: val.batteryCar,
                    staffId: val.staffId,
                    key: index,
                    look: ''
                })
            })
            console.log(mydata)

            this.setState({
                data: mydata,
            });

        })
    }

    //====================================用户请求（死数据）=============================================


    // UNSAFE_componentWillMount() {
    //     // const myData=[];
    //     // for (let i = 0; i < 46; i++) {
    //     //     myData.push({
    //     //         key: i,
    //     //         name: `员工${i}`,
    //     //         phone: `${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}`,
    //     //         age: `${i+1}`,
    //     //         sex: `男. ${i}`,
    //     //         sta:`正常`,
    //     //         car:`可用 `,
    //     //         mobile:`不可用 `,
    //     //         origin:`无 `,
    //     //         look:''
    //     //     });
    //     // }
    //     // this.setState({
    //     //     data:myData
    //     // })
    // }
    // componentWillUpdate(nextProps, nextState, nextContext) {
    // }


    //=================================导入=========================
    exportExcel = () => {
        const datas = this.state.data ? this.state.data : ""; //表格数据
        var option = {};
        let dataTable = []; //新建数组放数据
        console.log(datas);
        if (datas) {
            for (const data of datas) {
                console.log(data);
                if (data) {
                    let obj = {
                        // key: data.key,
                        // userName: data.userName,
                        // userTel: data.userTel,
                        // latestUse: data.latestUse,
                        // deliveryNo: data.deliveryNo,
                        // site: data.site,
                        // state: data.state,
                        name: data.name,
                        phone: data.phone,
                        age: data.age,
                        sex: data.sex,
                        sta: data.sta,
                        car: data.car,
                        mobile: data.mobile,
                        origin: data.origin,
                    };
                    dataTable.push(obj);
                }
            }
        }
        console.log(dataTable);
        option.fileName = "会员信息表"; //文件名
        option.datas = [
            {
                sheetData: dataTable, //数据
                sheetName: "会员信息表", //sheet名字 // sheetFilter: [dataTable.id, dataTable.name],//列过滤 // sheetFilter 列过滤(只有在 data 为 object 下起作用)(可有可无)
                sheetHeader: [

                    "姓名",
                    "电话",
                    "性别",
                    "年龄",
                    "状态",
                    "面包车",
                    "电瓶车",

                ], //// 第一行
            },
        ];
        var toExcel = new ExportJsonExcel(option);
        toExcel.saveExcel(); //保存
    };
    //===============================================================



    render() {
        console.log("触发render");
        console.log(this.state.data);
        // let myData = this.state.data;
        //====================表格里的数据在这里设置==================
        //         const data = [];

        //====================================表格======================================
        const columns = [
            {
                title: '姓名',
                dataIndex: 'name',
            },
            {
                title: '电话',
                dataIndex: 'phone',
            },
            {
                title: '性别',
                dataIndex: 'sex',
            },
            {
                title: '年龄',
                dataIndex: 'age',
            },
            {
                title: '状态',
                dataIndex: 'sta',
            },
            {
                title: '面包车',
                dataIndex: 'car',
            },
            {
                title: '电瓶车',
                dataIndex: 'mobile',
            },
            {
                title: '操作',
                dataIndex: 'look',
                render: (text, record) => (
                    //这里可以绑定key值来设置路由跳转

                    <Space size="middle">
                        <span  onClick={() => this.showModal(record.key)} className='bton'>修改</span>
                        <span onClick={() => this.showConfirm(record)}  className='bton'>查看</span>
                    </Space>
                )
            }

        ];


        //============================表格==================================
        const { selectedRowKeys } = this.state;
        const rowSelection = {
            selectedRowKeys,
            onChange: this.onSelectChange,
            selections: [
           
                {
                    key: 'odd',
                    text: '基数表格',
                    onSelect: changableRowKeys => {
                        let newSelectedRowKeys = [];
                        newSelectedRowKeys = changableRowKeys.filter((key, index) => {
                            if (index % 2 !== 0) {
                                return false;
                            }
                            return true;
                        });
                        this.setState({ selectedRowKeys: newSelectedRowKeys });
                    },
                },
                {
                    key: 'even',
                    text: '偶数表格',
                    onSelect: changableRowKeys => {
                        let newSelectedRowKeys = [];
                        newSelectedRowKeys = changableRowKeys.filter((key, index) => {
                            if (index % 2 !== 0) {
                                return true;
                            }
                            return false;
                        });
                        this.setState({ selectedRowKeys: newSelectedRowKeys });
                    },
                },
            ],
        };
        return (
            <div>
                <div className="DistributionBox">
                    <Form
                        {...layout}
                        id="Distribution-search"
                        name="Distribution-search"
                        onFinish={this.onFinish}
                        className='Distributionb'
                    >
                        <Form.Item label="关键字：" name={`search`} htmlFor="keywords" className="DistributionSearch">
                            <Input id="keywords" placeholder="请输入用户名或电话" />
                        </Form.Item>

                        <Form.Item label="状态：" htmlFor="state" name={`select`} className="DistributionState" >
                            <Select>
                                <Option value="正常">正常</Option>
                                <Option value="冻结">冻结</Option>
                            </Select>
                        </Form.Item>

                        <Form.Item className="DistributionStatebutton" {...tailLayout}>
                            <Button htmlType="submit" onFinish={this.onFinish}  >搜索</Button>
                        </Form.Item>
                    </Form>
                    <div className="Distributionbtn">
                        <Radio.Button value="default" onClick={() => { this.DelConfirm(this.state.selectedRowKeys) }}  className='Distributionbtn1'>批量删除</Radio.Button>
                        <Radio.Button value="default" onClick={() => { this.frozenConfirm(this.state.selectedRowKeys) }} className='Distributionbtn1'>冻结</Radio.Button>
                        <Radio.Button value="default" onClick={() => { this.thawConfirm(this.state.selectedRowKeys) }}  className='Distributionbtn1'>解冻</Radio.Button>
                        <Radio.Button value="default" onClick={() => { this.showModal(this.state.add) }} className='Distributionbtn1'>新增</Radio.Button>
                        <Button onClick={this.exportExcel} className='Distributionbtn1'>导出</Button>
                    </div>
                    
                </div>

                <div className="Distributiontab">
                    <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.data} />
                </div>

                {/*{===============  新增弹框==============修改弹框================}*/}
                <div className="myModal">
                    <Modal
                        title=""
                        okText='确认'
                        cancelText='取消'
                        visible={this.state.visible}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                        destroyOnClose={this.state.destroyOnClose}
                    >
                        <label>姓名：</label>
                        <Input defaultValue={this.state.name} ref={this.input_name} />
                        <label>电话：</label>
                        <Input defaultValue={this.state.phone} ref={this.input_phone} />
                        <label>性别：</label>
                        <Input defaultValue={this.state.sex} ref={this.input_sex} />
                        <label>年龄：</label>
                        <Input defaultValue={this.state.age} ref={this.input_age} type={"number"} min='0' max='100' />
                        <label>状态：</label>
                        <Input defaultValue={this.state.sta} ref={this.input_sta} type={"string"} disabled />
                        <label>面包车：</label>
                        <Input defaultValue={this.state.car} ref={this.input_car} />
                        <label>电瓶车：</label>
                        <Input defaultValue={this.state.mobile} ref={this.input_mobile} />
                    </Modal>
                </div>


            </div>
        );
    }
}

export default Distribution
