import React, { Component } from "react";
import '../css/wmx-css/wmx_Login.css'
// 使用antd组件实现登陆页
import {withRouter} from 'react-router-dom'

import { Layout,Form, Input, Button, Checkbox } from 'antd';
import {inject,observer} from 'mobx-react'
const { Header, Content } = Layout;

@inject('user')

@observer
class Login extends Component {
  // constructor(props) {
  //   super(props);
  //   this.state = {};
  // }

  render() {
      const layout = {
          labelCol: { span: 10 },
          wrapperCol: { span: 6 },
      };
      const tailLayout = {
          wrapperCol: { offset: 10, span: 6 },
      };
      const onFinish = values => {
          console.log('Success:', values);
          // 点击确认是调用user里的login方法来发送请求
          this.props.user.login(values).then(data =>{
              console.log(data);
              sessionStorage.setItem('LoginInfo',data);
              this.props.history.push({pathname:'/index', query:data})
          }).catch(err=>{
              console.log(err);
          })
      };

      const onFinishFailed = errorInfo => {
          console.log('Failed:', errorInfo);
      };
          return (
             <div>
          <Layout>
          {/*   头部 */}
            <Header>小U到家总后台</Header>
            <Content>
                <Form
                    {...layout}
                    name="basic"
                    initialValues={{ remember: true }}
                    onFinish={onFinish}
                    onFinishFailed={onFinishFailed}
                >
                    <Form.Item
                        label="姓名"
                        name="username"
                        rules={[{ required: true, message: '请输入姓名!' }]}
                    >
                        <Input />
                    </Form.Item>

                    <Form.Item
                        label="密码"
                        name="password"
                        rules={[{ required: true, message: '请输入密码!' }]}
                    >
                        <Input.Password />
                    </Form.Item>

                    <Form.Item {...tailLayout} name="remember" valuePropName="checked">
                        <Checkbox>Remember me</Checkbox>
                    </Form.Item>

                    <Form.Item {...tailLayout}>
                        <Button type="primary" htmlType="submit">
                            确认
                        </Button>
                    </Form.Item>
                </Form>
            </Content>
          </Layout>
        </div>
    )
  }
}

export default withRouter(Login);