import React, {Component} from 'react'
import "../css/wmx-css/wmx_Basicset.css"
import {Input, Button} from 'antd';
import Axios from "../util/Axios";
const {TextArea} =Input;

class BasicSetment extends Component {
    constructor(props) {
        super(props);
        this.input_phoneNumber =React.createRef();
        this.input_userAgreement =React.createRef();
        this.state ={
            phoneNumber:'',
            userAgreement:'',
        }
    }
    SaveIp =() =>{
        console.log("保存客户电话和内容====");
        console.log(this.input_phoneNumber.current.state.value);
        console.log(this.input_userAgreement.current.state.value);

    //     发请求保存数据
        Axios.post("/basicSettings",
            {phoneNumber:this.input_phoneNumber.current.state.value,
                userAgreement:this.input_userAgreement.current.state.value})
            .then((data) =>{
                console.log(data)
            }).catch((err) =>{
                console.log(err +" b保存失败")
        })

    };
    render() {
        return (
            <div>
                <div className="pro">
                    <label className="pad">客户电话:</label>
                    <Input  style={{width:300}} ref ={this.input_phoneNumber} defaultValue={this.state.phoneNumber}/>
                </div>

                <div className="pro">
                    <label className="pad">用户协议:</label>
                    <TextArea style={{width:300,height:200}} ref ={this.input_userAgreement}/>
                </div>

                <Button className="Btn" onClick={() =>this.SaveIp()}>保存</Button>
            </div>
        )
    }
}

export default BasicSetment;