import React, {Component} from 'react'
import '../css/wmx-css/wmx_Material.css'
import {Input,Select,Table,Button,Card,Modal} from 'antd'
import Axios from "../util/Axios";

import {Link} from "react-router-dom";

const {Option} =Select;
class MaterialStatistics extends Component {
    constructor(props) {
        super(props);
        this.input_myname = React.createRef();
        this.input_unitPrice =React.createRef();
        this.input_unit =React.createRef();
        this.input_unitCost =React.createRef();
        this.input_supCost =React.createRef();
        this.input_siteName =React.createRef();
        this.huaYang =React.createRef();

        this.state={
            SelectedRow: [], // 选中的行
            columns: [
                {
                    title: '物料名称',
                    className: 'column-money',
                    dataIndex: 'supName',
                    align: 'center',
                },
                {
                    title: '库存数量',
                    dataIndex: 'supCount',
                    align: 'center',
                },
                {
                    title: '单位',
                    dataIndex: 'unit',
                    align: 'center',
                },
                {
                    title: '单件成本（元）',
                    dataIndex: 'unitCost',
                    align: 'center',
                },
                {
                    title: '单价(元)',
                    dataIndex: 'supUnitPrice',
                    align: 'center',
                },
                {
                    title: '总成本(元)',
                    dataIndex: 'totalCost',
                    align: 'center',
                },
                {
                    title: '所属站点',
                    dataIndex: 'siteName',
                    align: 'center',
                },
                {
                    title: '操作',
                    dataIndex: 'operate',
                    align: 'center',
                    className:"noShow",
                },
            ],
            data :[
                // {
                //     key: '1',
                //     NameMaterial: '卷纸',
                //     unit: '个',
                //     cost: '1',
                //     look:'查看',
                //     number:"100",
                //     address:"青羊区"
                // }
            ],
            ModalText:"content",
            visible:false,
            confirmLoading:false,
            supName:'',
            supUnitPrice:'',
            unit:'',
            unitCost:'',
            supCount:'',
            siteName:'',
            siteId:'',
            supId:'',
            values:''
        }
    }

    // 挂载阶段开始获取数据
    componentDidMount() {

        this.getAll();
    }
    getAll =() =>{
        Axios.get("/materialManagement").then((res) =>{
            console.log(res.data.data);
            this.copyData(res.data.data);
        }).catch((err) =>{
            console.log(err+ "报错了")
        })
    };

    copyData =(data) =>{

        let myData =[];

        for (let i =0 ;i < data.length ; i++){
            myData.push({
                key :i,
                supName:data[i].supName,
                supUnitPrice: data[i].supUnitPrice,
                supCount:data[i].supCount,
                unit:data[i].unit,
                unitCost:data[i].unitCost,
                siteName:data[i].siteName,
                totalCost:data[i].totalCost,
                operate:"查看"
            })
        }
        console.log(myData);
        this.setState({
            data:myData
        })
    };
    //
    DeleteD = () =>{
        console.log("====删除");
        let {selectedRows} = this.state;

        let newArr =  selectedRows.map((item) => {
            // console.log(item.siteId);
            let obj = {};
            obj.siteId = item.siteId;
            obj.supId  =item.supId;
            return obj
        });

        Axios.post("/materialManagement/delete",{suppLiesId : newArr,}).then((data) =>{
            console.log(data);
            if (data.status ===200){
                this.getAll();
            }
        }).catch((err)=>{
            console.log(err)
        })
    };
    //搜索内容

    SearchInputValue =(event) =>{
        this.setState({
            InputValue:event.target.value,
        })
    };
     handleChange =(value)  =>{
        // console.log(value);
        // console.log(`Option ${value}`)
         this.setState({
             values:`${value}`
         });
         // this.mySearch(`${value}`)
    };
    mySearch =() =>{
        let {values} =this.state;
        console.log(values);
        const {InputValue} = this.state;
        console.log(InputValue,"-----这是值");

        Axios.post("/materialManagement/blurrySupplies",{siteName:values,suppliesName:InputValue }).then((data) =>{
            console.log(data);
            console.log(data.data.data);
            if (data.data.code === '0000'){
                this.copyData(data.data.data);
            }
            // this.setState.data = data.data.data
        }).catch((err) =>{
            console.log(err);
        });
        // if (InputValue ===this.state.data.NameMaterial ){
        //     return InputValue;
        // }
    };

    onSelectChange =(selectedRowKeys, selectedRows) =>{
        console.log("selectedRowKeys changed: ", selectedRowKeys);
        console.log("selectedRow changed: ", selectedRows);
        this.setState({
            selectedRowKeys,
            selectedRows,
        })
    };

    // 模态框
    showModal = () => {
        let {selectedRows} =this.state;
        this.setState({
            visible: true,
        });
        console.log(selectedRows);
        // 修改的数据
        for (let i =0 ;i< selectedRows.length; i++){
            // console.log(selectedRows[i].supName);
            // console.log(selectedRows[i].unitPrice);
            // console.log(selectedRows[i].supCost);
            // console.log(selectedRows[i].supId);
            // console.log(selectedRows[i].siteId);
            this.setState({
                supName:selectedRows[i].supName,
                supUnitPrice:selectedRows[i].unitPrice,
                unit:selectedRows[i].unit,
                unitCost:selectedRows[i].unitCost,
                supCount:selectedRows[i].supCost,
                siteName:selectedRows[i].siteName,
                supId:selectedRows[i].supId,
                siteId:selectedRows[i].siteId,
            })
        }
    };
    handleOk = () => {
        console.log("这是确认");
        this.setState({
            ModalText: 'The modal will be closed after two seconds',
            confirmLoading: true,
        });
        // 发送请求修改数据
        console.log(this.input_myname.current.state.value);
        Axios.post('/materialManagement',{
            supName: this.input_myname.current.state.value,
            supUnitPrice: this.input_unitPrice.current.state.value,
            unit: this.input_unit.current.state.value,
            unitCost: this.input_unitCost.current.state.value,
            supCount: this.input_supCost.current.state.value,
            siteName: this.input_siteName.current.state.value,
            siteId:this.state.siteId,
            supId:this.state.supId,
        }).then((data) =>{
            console.log(data)
        }).catch((err) =>{
            console.log(err);
        });
        setTimeout(() => {
            this.setState({
                visible: false,
                confirmLoading: false,
            });
        }, 2000);
    };
    handleCancel = () => {
        console.log('这是关闭');
        this.setState({
            visible: false,
        });
    };
    render(){

        const {SelectedRow} = this.state;
        const rowSelection={
            SelectedRow,
            onChange: this.onSelectChange
        };
        const { visible, confirmLoading } = this.state;
        return (
            <div>
                <label>关键字：</label>
                <Input style={{width:200}} placeholder="模糊搜索" onChange={this.SearchInputValue}/>

                <label className="label" >所属站点：</label>
                <Select  style={{ width: 200 }} onChange={(value)=>this.handleChange(value)}>
                    <Option key={1} value={'华阳店'}> 华阳店</Option>
                    <Option key={2} value={'武侯店'}> 武侯店</Option>
                    <Option key={3} value={'金牛店'}> 金牛店</Option>
                </Select>,
                <div className="Material">
                    按物料名称进行搜索
                </div>
                <Card>
                    <Button className="R"  value="default" onClick={()=> this.mySearch()}>搜索</Button>
                </Card>

                {/*  渲染的表格*/}
                <Table
                    rowSelection={rowSelection}
                    columns={this.state.columns}
                    dataSource ={this.state.data}
                    bordered
                    title= {()=><header className="sp">
                        <Link to={"/index/Addition/0"}>
                            <span>添加物料种类</span>
                        </Link>
                        <span onClick={this.showModal}>编辑</span>
                        <Modal
                            title="确认修改"
                            visible={visible}
                            onOk={this.handleOk}
                            confirmLoading={confirmLoading}
                            onCancel={this.handleCancel}
                            width={900}
                            okText={"确定"}
                            cancelText={"取消"}
                        >
                            <div className="mar">
                                <label>物料名称：</label>
                                <Input style={{width:300}} defaultValue={this.state.supName} ref={this.input_myname} />
                            </div>
                            <div className="mar">
                                <label>物料单价：</label>
                                <Input style={{width:300}} defaultValue={this.state.supUnitPrice} ref={this.input_unitPrice}/>
                            </div>
                            <div className="mar">
                                <label>物料单位：</label>
                                <Input style={{width:300}} defaultValue={this.state.unit} ref={this.input_unit}/>
                            </div>

                            <div className="mar">
                                <label>物料成本：</label>
                                <Input style={{width:300}} defaultValue={this.state.unitCost} ref={this.input_unitCost}/>
                            </div>
                            <div className="mar">
                                <label>物料数量：</label>
                                <Input style={{width:300}} defaultValue={this.state.supCount} ref={this.input_supCost}/>
                            </div>
                            <div className="mar">
                                <label>物料站点：</label>
                                <Input style={{width:300}} defaultValue={this.state.siteName} ref={this.input_siteName}/>
                            </div>
                        </Modal>
                        <span onClick={() =>this.DeleteD()}>删除</span>
                    </header>}
                />
            </div>

        )
    }
}

export default MaterialStatistics