import React, { Component } from 'react'
import { Button, Col, Form, Input, Row, Space, Table, message } from "antd";
import 'moment/locale/zh-cn';

import Axios from '../../../util/Axios'


import '../../../css/ZhanDianHouTai/FindReadList.css'



class FindReadList extends Component {
  constructor() {
    super();
    this.state = {
      selectedRowKeys: [],
      selectedRows: [],
      dispatchList: '',
      columns: [
        { title: '订单编号', dataIndex: 'orderNum', align: 'center' },
        { title: '货物类别', dataIndex: 'goodsCategory', align: 'center' },
        { title: '商品数量', dataIndex: 'goodsNum', align: 'center' },
        { title: '箱数', dataIndex: 'boxNum', align: 'center' },
        { title: '金额', dataIndex: 'price', align: 'center' },
        { title: '重量', dataIndex: 'goodsHeight', align: 'center' },
        { title: '收件人', dataIndex: 'recipient', align: 'center' },
        { title: '联系方式', dataIndex: 'Contact', align: 'center' },
        { title: '审核时间', dataIndex: 'audiTime', align: 'center' },
        { title: '时效产品', dataIndex: 'agingProduct', align: 'center' },
        { title: '地址', dataIndex: 'address', align: 'center' },
        // {
        //   title: '操作',
        //   dataIndex: 'bind_operation',
        //   align: 'center',
        //   render: (text, record, index) => <Button type="link" size='small' onClick={() => this.getOrderMsg(record)}>查看</Button>
        // },
      ]
    }
  }

  componentDidMount() {
    Axios.get('/marki/selectUndelivered')
      .then(res => {
        console.log(res);
        if(res.data.msg === '查询未派送订单成功'){
          this.getList(res.data)

        }
      })
  }

  //全查询  
  getList = (val) => {
    let myData = [];
    if(val.data){
      val.data.forEach((item, index) => {
        myData.push({
          key: index,
          orderNum: item.orderId,
          goodsCategory: item.parentName,
          goodsNum: item.count,
          boxNum: item.supCount,
          price: item.price,
          goodsHeight: item.heavy,
          recipient: item.consigneeName,
          Contact: item.consigneePhoneNumber,
          audiTime: item.checkTime,
          agingProduct: item.agingCode,
          address: item.consigneeAddress,
          // bind_operation: '查看'
        })
      });

    }
    //2.修改State
    this.setState({
      dispatchList: myData,
    });

  };

  resultQuery = () => {
    Axios.get('/marki/selectUndelivered')
      .then(res => {
        console.log(res);
        this.getList(res.data)
      })
  }
  //表格行的多选和全选
  onSelectChange = (selectedRowKeys, selectedRows) => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    console.log(selectedRows);
    this.setState({ selectedRowKeys, selectedRows });
  };
  //选择时间范围的表单变化触发的函数

  handleSelectTime = (value, dateString) => {
    console.log('选择的时间：', dateString)
    this.setState({
      startDistribution: dateString[0],
      endDistribution: dateString[1],
    })
  };


  // 多条件搜索的表单
  getFields = () => {
    const children = [];
    children.push(
      <Col span={6} key='1'>
        <Form.Item
          name={`searchKeyword`}
          label={`关键字:`}
        >
          <Input size="small" placeholder="请输入订单号/姓名" />
        </Form.Item>
      </Col>,


      <Col
        span={4}
        key='4'
        style={{
          textAlign: 'center',
        }}
      >
        <Space size='large'>
          <Button type="primary" htmlType="submit">搜索</Button>
          <Button onClick={this.resultQuery}>重置</Button>
        </Space>

      </Col>


    );
    // console.log(children)
    return children;
  };
  //查看详情
  getOrderMsg = (val) => {
    console.log(val)
    this.props.history.push({
      pathname: '/DispatchDetails',
      state: { record: val }
    })

  }
  //新建物流单
  newLogistics = () => {
    // console.log('==================')
    this.props.history.push({
      pathname: '/index/NewLogistics'
    })
  };
  //派单
  toDistribute = () => {
    console.log(this.state.selectedRows);
    if (this.state.selectedRows.length > 0) {
      this.props.history.push({
        pathname: '/index/Distribute',
        state: { orderid: this.state.selectedRows[0].orderNum }
      })
    } else {
      message.error('请选择您要派出的订单！！');
    }

  };
  render() {
    const { selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };

    //多条件搜索表单验证  得到表单的数据
    const onFinish = values => {

      console.log(values.searchKeyword);
      if (values.searchKeyword) {
        Axios.post('/marki/vagueSelectUndelivered', {
          // params: {
          vagueSelect: values.searchKeyword
          // }
        }).then(res => {
          console.log(res);
          this.getList(res.data)
        })
      }else{
        Axios.get('/marki/selectUndelivered')
        .then(res => {
          console.log(res);
          this.getList(res.data)
        })
      }


    };
    return (
      <div id='findReadList'>
        <div className='orderSearch'>
          <Form
            name="orderSearch"
            className="ant-advanced-search-form"
            onFinish={onFinish}
          >
            <Row>{this.getFields()}</Row>
          </Form>
        </div>
        <div id='delivery'>
          <Space size={100}>
            <Button size='small' onClick={this.newLogistics}>新建物流单</Button>
            <Button size='small' onClick={this.toDistribute}>派单</Button>
          </Space>
        </div>
        <div id='giveList'>
          <Table locale={{ emptyText: '暂无数据' }} size='small' rowSelection={{
              type: 'radio',
              ...rowSelection
            }} columns={this.state.columns} dataSource={this.state.dispatchList} bordered />
        </div>
      </div>
    )
  }
}

export default FindReadList