import React, { Component } from 'react'
import { Button, Divider, Table, Typography, Input, Row, Col,message } from "antd";
import Axios from '../../../util/Axios'

import '../../../css/ZhanDianHouTai/DistributionEditor.css'

const { Title } = Typography;


export default class DistributionEditor extends Component {
  constructor(props) {
    super(props);
    this.state = {
      myKey:'',
      newName: '',
      nwePhone: '',
      newAddres: '',
      orderDetailsMsg: '',//接受订单管理页面发送的数据
      orderDetails: '',//订单详情表格的数据
      columns: [
        {
          title: '商品1级分类',
          dataIndex: 'classification1',
          align: 'center'
        },
        {
          title: '商品2级分类',
          dataIndex: 'classification2',
          align: 'center'
        },
        {
          title: '数量',
          dataIndex: 'goodsNum',
          align: 'center'
        }
      ]
    }
  }
  // 初始化订单详情发送过来的数据
  componentDidMount() {
    // console.log(this.props.location.state);
    // console.log(this.props.location.state.signNumber);

    // console.log(this.state.orderDetailsMsg)
    // console.log(this.state.orderDetailsMsg.orderNumber)
    Axios.post('/Order/findOderByLg', {
      logisticsId: this.props.location.state.key
    })
      .then(res => {
        console.log(res);
        console.log(res.data);
        let myData = [];
        if(res.data.code === '0000'){
          res.data.data.orderOneToThreeList.forEach((val, index) => {
            myData.push({
              key: index,
              classification1: val.twoName,
              classification2: val.threeName,
              goodsNum: val.count
            })
          });
          console.log(myData)
          this.setState({
            orderDetails: myData,
            orderDetailsMsg: res.data.data,
            myKey:this.props.location.state.orderId
          })
        }


      })

  }


  //返回上一页
  goBackPage = () => {
    window.history.back(-1)
  };
  changeName = e => {
    this.setState({
      newName: e.target.value
    })
  }
  changePhone = e => {
    this.setState({
      newPhone: e.target.value
    })
  }
  changeAddres = e => {
    this.setState({
      newAddres: e.target.value
    })
  }
  submitVal = () => {
    console.log(this.state.newName);
    console.log(this.state.newPhone);
    console.log(this.state.newAddres);
    console.log(this.state.myKey);

    Axios.put('/zhouzhouController/updateConsignee', {
      // params: {
        consigneePhoneNumber: this.state.newPhone,
        consigneeName: this.state.newName,
        consigneeAddress: this.state.newAddres,
        orderId:this.state.myKey
      // }

    })
      .then(res => {
        console.log(res);
        if(res.data.code === '0000'){
          message.success('修改成功');
          this.props.history.push('/index/Delivery')
        }else{
          message.error('修改失败');
        }
      })
  }
  render() {
    //控制是否有分页显示
    let paginationProps = '';
    if (this.state.orderDetails.length >= 10) {
      paginationProps = true
    } else {
      paginationProps = false

    }
    // console.log(this.state.orderDetailsMsg.consigneeName);
    return (
      <div id='distribution'>
        <div className='detailsTitle'>
          <Button size="small" onClick={this.goBackPage}>返回</Button>
          <p>订单状态：{this.state.orderDetailsMsg.signStatus}</p>
        </div>
        <Divider />
        <div>
          <Row className='rowBox' gutter={24}>
            <Col span={4} key='1'>
              <p>订单编号：{this.state.orderDetailsMsg.id}</p>
            </Col>
            <Col span={4} key='2'>
              <p>用户：{this.state.orderDetailsMsg.userName}</p>
            </Col>
            <Col span={4} key='3'>
              <p>
                <Input addonBefore='收货人:' size='small' placeholder={this.state.orderDetailsMsg.consigneeName} onChange={this.changeName} />
              </p>
            </Col>
            <Col span={4} key='4'>
              <p>
                <Input addonBefore='收货人电话:' size='small' placeholder={this.state.orderDetailsMsg.consigneePhoneNumber} onChange={this.changePhone} />

              </p>
            </Col>
            <Col span={4} key='5'>
              <p>
                <Input addonBefore='收货人地址:' size='small' placeholder={this.state.orderDetailsMsg.consigneeAddress} onChange={this.changeAddres} />
              </p>
            </Col>
          </Row>
          <Row className='rowBox' gutter={24}>
            <Col span={4} key='1'>
              <p>配送员：{this.state.orderDetailsMsg.staffName}</p>
            </Col>
            <Col span={4} key='2'>
              <p>配送员电话：{this.state.orderDetailsMsg.staffPhoneNumber}</p>
            </Col>
            <Col span={4} key='3'>
              <p>重量：{this.state.orderDetailsMsg.heavy}</p>
            </Col>
            <Col span={4} key='4'>
              <p>所属街道：{this.state.orderDetailsMsg.street}</p>
            </Col>
            <Col span={4} key='5'>
              <p>所属站点：{this.state.orderDetailsMsg.siteName}</p>
            </Col>
          </Row>
          <Row>
            <Col
              span={24}
              style={{
                textAlign: 'right',
              }}
            >
              <Button type="primary" onClick={this.submitVal}>确认修改</Button>
            </Col>
          </Row>
        </div>
        <Divider />

        <div id="shoppingVoucher">
          <Input defaultValue={this.state.orderDetailsMsg.cashNumber} />
          <Title level={4}>购物凭条：</Title>
          <div className="VoucherMsg">
            <div>
              <p>收银条台号:</p>
              <p>{this.state.orderDetailsMsg.cashNumber}</p>
            </div>
            <div>
              <p>流水号:</p>
              <p>{this.state.orderDetailsMsg.serialNumber}</p>
            </div>
            <div>
              <p>日期:</p>
              <p>{this.state.orderDetailsMsg.checkTime}</p>
            </div>
          </div>

        </div>
        <Divider />
        <div id='orderDetails'>

          <Title level={4}>订单详情：</Title>
          <div>
            <Table locale={{emptyText:'暂无数据'}} size='small' columns={this.state.columns} dataSource={this.state.orderDetails} bordered style={{ width: 300 }}
              pagination={paginationProps} />
            <p>{this.state.orderDetailsMsg.supName}：{this.state.orderDetailsMsg.supCount}</p>
            <p>重量：{this.state.orderDetailsMsg.heavy} KG</p>
            <p>金额：{this.state.orderDetailsMsg.price} 元</p>
            <p>消耗{this.state.orderDetailsMsg.supName}：{this.state.orderDetailsMsg.supCount} 个</p>
          </div>
          <div>
            <p>订单情况：</p>
            <p>站点派送时间：{this.state.orderDetailsMsg.deliveryTime}</p>
          </div>
          <div>
            <p>配送员取货时间：{this.state.orderDetailsMsg.pickTime}</p>
            <p>配送员完成时间：{this.state.orderDetailsMsg.finishTime}</p>
          </div>
        </div>
      </div>
    )
  }
}
