import React, { Component } from 'react'
import { Button, Divider, Table, Typography, message } from "antd";


import '../../../css/ZhanDianHouTai/DispatchDetails.css'
import Axios from '../../../util/Axios';

const { Title } = Typography;

class DispatchDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      courierMsg: '',
      orderDetailsMsg: '',//接受订单管理页面发送的数据
      orderDetails: '',//订单详情表格的数据
      columns: [
        {
          title: '商品1级分类',
          dataIndex: 'classification1',
          align: 'center'
        },
        {
          title: '商品2级分类',
          dataIndex: 'classification2',
          align: 'center'
        },
        {
          title: '数量',
          dataIndex: 'goodsNum',
          align: 'center'
        }
      ]
    }
  }
  // 初始化订单详情发送过来的数据
  UNSAFE_componentWillMount() {
    // console.log(this.props.location.state.DetailsMsg);

    // const data = [];
    // for (let i = 0; i < 3; i++) {
    //     data.push({
    //         key: i,
    //         classification1: '服装',
    //         classification2: '衣服',
    //         goodsNum: 123 + i
    //     });
    // }
    // this.setState({
    //   orderDetailsMsg: this.props.location.state.DetailsMsg,
    //   // orderDetails:data
    // })
  }

  componentDidMount() {
    let myData = [];
    this.props.location.state.DetailsMsg.orderInfo.forEach((item, index) => {
      myData.push({
        key: index,
        classification1: item.parentName,
        classification2: item.sonName,
        goodsNum: item.count
      })
    });
    this.setState({
      orderDetailsMsg: this.props.location.state.DetailsMsg.orderInfo[0],
      orderDetails: myData,
      courierMsg: this.props.location.state.DetailsMsg.courierId
    })
  }
  //返回上一页
  goBackPage = () => {
    window.history.back(-1)
  };

  // 平台派单
  platformDispatch = () => {
    console.log("================")
    let that = this
    let courierId = this.state.courierMsg.staffId;
    let orderId = this.state.orderDetailsMsg.orderId;
    console.log(this.state.courierMsg.staffId);
    console.log(this.state.orderDetailsMsg.orderId);
    Axios.post('/marki/platformDispatch', {
      courierId: courierId,
      orderId: orderId
    }).then(res => {
      console.log(res);
      that.props.history.push({
        pathname: '/index/FindReadList',
      })
      if (res.data.msg === "平台派单成功") {
        message.success('平台派送成功');

      } else {
        message.error('派单失败！！请稍候再试');
      }
    })
  }
  render() {
    console.log(this.state);
    //控制是否有分页显示
    let paginationProps = '';
    if (this.state.orderDetails.length >= 10) {
      paginationProps = true
    } else {
      paginationProps = false

    }
    return (
      <div id='DispatchDetails'>
        <div className='detailsTitle'>
          <Button size="small" onClick={this.goBackPage}>返回</Button>
          <p>订单状态：{this.state.orderDetailsMsg.signStatus}</p>
        </div>
        <Divider />
        <div id='detailsMsg'>
          <div>
            <p>订单编号：{this.state.orderDetailsMsg.orderId}</p>
            <p>配送员：{this.state.courierMsg.staffName}</p>
          </div>
          <div>
            <p>用户：{this.state.orderDetailsMsg.userName}</p>
            <p>配送员电话：{this.state.courierMsg.staffPhoneNumber}</p>
          </div>
          <div>
            <p>收货人：{this.state.orderDetailsMsg.consigneeName}</p>
            <p>重量：{this.state.orderDetailsMsg.heavy}KG</p>
          </div>
          <div>
            <p>收货人电话：{this.state.orderDetailsMsg.consigneePhoneNumber}</p>
            <p>所属街道：{this.state.orderDetailsMsg.street}</p>
          </div>
          <div>
            <p>收货地址：{this.state.orderDetailsMsg.consigneeAddress}</p>
          </div>
        </div>
        <Divider />


        <div id='orderDetails'>
          <Title level={4}>订单详情：</Title>
          <div>
            <Table locale={{ emptyText: '暂无数据' }} size='small' columns={this.state.columns} dataSource={this.state.orderDetails} bordered style={{ width: 300 }}
              pagination={paginationProps} />
            <p>{this.state.orderDetailsMsg.supName}：{this.state.orderDetailsMsg.supCount}</p>
            <p>重量：{this.state.orderDetailsMsg.heavy}</p>
            <p>金额：{this.state.orderDetailsMsg.price}</p>
            <p>消耗{this.state.orderDetailsMsg.supName}：{this.state.orderDetailsMsg.supCount}</p>
          </div>
          <div id='toPeiSon'>
            <Button onClick={this.platformDispatch}>派单</Button>
          </div>
        </div>
      </div>
    )
  }
}

export default DispatchDetails