import React, { Component } from 'react'
import { Input, Select, Button, Space, Table, Form, Row, Col, Popconfirm, DatePicker } from 'antd';
import ExportJsonExcel from "js-export-excel"; //引入到处excel表格的模块
import 'moment/locale/zh-cn';
import locale from 'antd/es/date-picker/locale/zh_CN';
import '../../css/zj/OrderManagementZ.css'

import Axios from '../../util/Axios'


const { Option } = Select;
const { RangePicker } = DatePicker;
// const {initialValues} = Form


class OrderManagementZ extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isloading: true,
      selectedRowKeys: [],//选中行的index
      selectedRows: [], //选中行的数据对象
      orderData: [],//订单列表的数据
      startDistribution: '',
      endDistribution: '',
      columns: [
        { title: '订单编号', dataIndex: 'orderNumber', align: 'center' },
        { title: '货物类别', dataIndex: 'goodsCategory', align: 'center' },
        { title: '商品数量', dataIndex: 'goodsQuantity', align: 'center' },
        { title: '箱数', dataIndex: 'casesNumber', align: 'center' },
        { title: '金额', dataIndex: 'goodsPrice', align: 'center' },
        { title: '重量', dataIndex: 'goodsHeight', align: 'center' },
        { title: '订单状态', dataIndex: 'orderStatus', align: 'center' },
        { title: '用户昵称', dataIndex: 'userName', align: 'center' },
        { title: '收件人', dataIndex: 'addressee', align: 'center' },
        { title: '联系方式', dataIndex: 'contactInformation', align: 'center' },
        { title: '配送人', dataIndex: 'distributor', align: 'center' },
        { title: '配送时间', dataIndex: 'deliveryTime', align: 'center' },
        { title: '时效产品', dataIndex: 'product', align: 'center' },
        { title: '所属站点', dataIndex: 'site', align: 'center' },
        {
          title: '操作',
          dataIndex: 'operation',
          align: 'center',
          render: (text, record, index) =>
            <Space className='operationBtns'>
              <Button type="link" size='small' onClick={() => this.getOrderMsg(record)}>查看</Button>
              <Popconfirm okText="删除" cancelText="取消" title="确定删除吗?" onConfirm={() => this.handleDelete(record)}>
                <Button type='link'>删除</Button>
              </Popconfirm>

            </Space>
        },
      ]

    }
  }

  componentDidMount() {
    Axios.post('/Order/findOrderList')
      .then(res => {
        console.log(res.data.msg);
        if (res.data.msg === '查询成功') {
          
          this.setState({ isloading: false })
        }
        console.log(res.data.data);
        this.getListS(res.data.data)
      })
  }

  getListS = (msg) => {
    let myData = [];
    msg.forEach((val, index) => {
      myData.push({
        key: index,
        logisticsId: val.logisticsId,
        orderNumber: val.id,
        goodsCategory: val.oneName,
        goodsQuantity: val.count,
        casesNumber: val.supCount,
        goodsPrice: val.price,
        goodsHeight: val.heavy,
        orderStatus: val.checkStatus,
        userName:val.userName,
        addressee: val.consigneeName,
        contactInformation: val.consigneePhoneNumber,
        distributor: val.staffName,
        deliveryTime: val.deliveryTime,
        product: val.agingCode,
        site: val.siteName,
        operation: '查看'
      });

    })
    //2.修改State
    this.setState({
      orderData: myData,
    });
  }
  //表格行的多选和全选
  onSelectChange = (selectedRowKeys, selectedRows) => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    console.log(selectedRows);
    this.setState({ selectedRowKeys, selectedRows });
  };
  //导出excel表
  exportExcel = () => {
    // console.log('========')
    const datas = this.state.orderData ? this.state.orderData : ""; //表格数据
    let option = {};
    let dataTable = []; //新建数组放数据
    // console.log(datas);
    if (datas) {
      for (const data of datas) {
        // console.log(data);
        if (data) {
          let obj = {
            key: data.key,
            orderNumber: data.orderNumber,
            goodsCategory: data.goodsCategory,
            goodsQuantity: data.goodsQuantity,
            casesNumber: data.casesNumber,
            goodsPrice: data.goodsPrice,
            goodsHeight: data.goodsHeight,
            orderStatus: data.orderStatus,
            addressee: data.addressee,
            contactInformation: data.contactInformation,
            distributor: data.distributor,
            deliveryTime: data.deliveryTime,
            product: data.product,
            site: data.site,
          };
          dataTable.push(obj);
        }
      }
    }
    console.log(dataTable);
    option.fileName = "会员信息表"; //文件名
    option.datas = [
      {
        sheetData: dataTable, //数据
        sheetName: "会员信息表", //sheet名字 // sheetFilter: [dataTable.id, dataTable.name],//列过滤 // sheetFilter 列过滤(只有在 data 为 object 下起作用)(可有可无)
        sheetHeader: [
          "订单编号",
          "货物类别",
          "商品数量",
          "箱数",
          "金额",
          "重量",
          "订单状态",
          "收件人",
          "联系方式",
          "配送人",
          "配送时间",
          "时效产品",
          "所属站点",
        ], //// 第一行
      },
    ];
    let toExcel = new ExportJsonExcel(option);
    toExcel.saveExcel(); //保存
  };

  //发送请求把订单列表该行的数据发送到下一个页面，并且跳转页面
  getOrderMsg = (record) => {
    console.log('=================')
    console.log(record)
    console.log(record.logisticsId)
    this.props.history.push({
      pathname: '/index/Zjj',
      state: { signNumber: record.logisticsId }
    })
  };
  // 删除列表中的项目
  handleDelete = key => {
    const idList = [];
    console.log(key);
    idList.push({id:key.orderNumber, lgId:key.logisticsId})
    console.log(idList);
    Axios.post('/Order/delteOrderbyLg', {
      lgId: idList
    })
      .then(res => {
        console.log(res);
        Axios.post('/Order/findOrderList')
        .then(res => {
          console.log(res.data.msg);
          if (res.data.msg === '查询成功') {
            
            this.setState({ isloading: false })
          }
          console.log(res.data.data);
          this.getListS(res.data.data)
        })
      })
      .catch(err => {
        console.log(err);
      })

  };
  //批量删除
  batchDeletion = () => {
    let idList = [];
    //将选中的数据中的id循环到我们自己定义的idList中
    const { selectedRows } = this.state;
    selectedRows.forEach(item => {
      idList.push({id:item.orderNumber,lgId:item.logisticsId})
    });
    console.log(idList);

    // /Order/delteOneOrderbyLg
    Axios.post('/Order/delteOrderbyLg', {
      lgId: idList
    })
      .then(res => {
        console.log(res);
        Axios.post('/Order/findOrderList')
        .then(res => {
          console.log(res.data.msg);
          if (res.data.msg === '查询成功') {
            
            this.setState({ isloading: false })
          }
          console.log(res.data.data);
          this.getListS(res.data.data)
        })
      })
      .catch(err => {
        console.log(err);
      })
  };
  //选择时间范围的表单变化触发的函数
  handleSelectTime = (value, dateString) => {
    console.log('选择的时间：', dateString);
    this.setState({
      startDistribution: dateString[0],
      endDistribution: dateString[1],
    })
  };
  // 多条件搜索的表单
  getFields = () => {
    // const count =  6;
    const children = [];
    const dateFormat = 'YYYY-MM-DD';
    children.push(
      <Col span={8} key='1'>
        <Form.Item
          name={`keyWords`}
          label={`关键字:`}
        // rules={[
        //   {
        //     required: true,
        //     message: '请输入搜索关键字!',
        //   },
        // ]}
        >
          <Input size="small" placeholder="模糊搜索" />
        </Form.Item>
        <p id='searchTitle'>输入用户昵称、收货人、收货人电话、配送员进行搜索</p>

      </Col>,
      <Col span={16} key='2'>
        <Form.Item
          label='选择时间范围：'
        >
          <RangePicker size='small' format={dateFormat} locale={locale} onChange={this.handleSelectTime} />
        </Form.Item>
        <p id='searchTitle'>以订单申请的时间为准</p>

      </Col>,

      <Col span={8} key='4'>
        <Form.Item
          name='checkStatus'
          label='订单状态：'
        // rules={[
        //   {
        //     required: true,
        //     message: '请选择订单状态!',
        //   },
        // ]}
        >
          <Select size="small" placeholder='选择订单状态'>
            <Option value="审核成功">审核成功</Option>
            <Option value="审核失败">审核失败</Option>
            <Option value="待审核">待审核</Option>
            <Option value="未审核">未审核</Option>
          </Select>
        </Form.Item>
      </Col>,
      <Col span={8} key='5'>
        <Form.Item
          name='siteName'
          label='所属站点：'
        // rules={[
        //   {
        //     required: true,
        //     message: '请选择所属!',
        //   },
        // ]}
        >
          <Select size="small" placeholder='选择门店'>
            <Option value="华阳店">华阳店</Option>
            <Option value="武侯店">武侯店</Option>
            <Option value="金牛店">金牛店</Option>

          </Select>
        </Form.Item>
      </Col>
    );
    // console.log(children)
    return children;
  };

  //重置查询
  resetQuery = () => {
    this.setState({ isloading: true });
    Axios.post('/Order/findOrderList')
      .then(res => {
        console.log(res.data.msg);
        if (res.data.msg === '查询成功') {
          this.setState({ isloading: false })
        }
        console.log(res.data.data);
        this.getListS(res.data.data)
      })
  };

  // =================================================================
  render() {
    //多条件搜索表单验证  得到表单的数据
    const onFinish = values => {
      this.setState({ isloading: true });
      values.deliveryTimestart = this.state.startDistribution;
      values.deliveryTimeend = this.state.endDistribution;
      // console.log('Received values of form: ', values);
      Axios.post('/Order/findSomeOrderList', values)
        .then(res => {
          console.log(res);
          if (res.data.msg === '查询成功') {
            this.setState({ isloading: false })
          }
          this.getListS(res.data.data)
        })
    };

    const { selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };

    return (
      <div id='allOrderSearch'>
        <div className='orderSearch'>
          <Form
            name="advanced_search"
            className="ant-advanced-search-form"
            onFinish={onFinish}
          >
            <Row gutter={24}>{this.getFields()}</Row>
            <Row>
              <Col
                span={24}
                style={{
                  textAlign: 'right',
                }}
              >
                <Space size='large'>
                  <Button type="primary" htmlType="submit">搜索</Button>
                  <Button onClick={this.resetQuery}>重置</Button>
                </Space>

              </Col>
            </Row>
          </Form>
        </div>
        <div className='orderList'>
          <div className='btns'>
            <Popconfirm okText="删除" cancelText="取消" title="确定删除吗?" onConfirm={this.batchDeletion}>
              <Button size="small" >批量删除</Button>
            </Popconfirm>
            <Button size="small" onClick={this.exportExcel}>导出</Button>
          </div>
          <div>
            <Table locale={{emptyText:'暂无数据'}} loading={this.state.isloading} size='small' rowSelection={rowSelection} columns={this.state.columns} dataSource={this.state.orderData} />
          </div>
        </div>
      </div>
    )
  }
}

export default OrderManagementZ