import React, {Component} from 'react'
import {Input, Select,Button} from 'antd';
import { DatePicker } from 'antd';
import moment from 'moment';
// import { Menu, Dropdown, Button, message, Tooltip } from 'antd';
// import { DownOutlined, UserOutlined } from '@ant-design/icons';
import { Table } from 'antd';
import ExportJsonExcel from "js-export-excel";
import '../../css/LiKeliang/Journal.css'
import Axios from '../../util/Axios'
const { RangePicker } = DatePicker;
const { Option } = Select;
const dateFormat = 'YYYY/MM/DD';
let num=0;
class Journal extends Component {
    constructor(){
        super();
        this.staffName=React.createRef()
        this.state={
            size: '',
            data:[],
            operationName:'',
            startTime:'',
            endTime:'',
            copyDate:[]
        }
    }
    // UNSAFE_componentWillMount() {
    //     const data = [];
    //     for (let i=0;i<10;i++){
    //         data.push({
    //             key:i,
    //             type:i,
    //             time:2018/10/i,
    //             man:i,
    //             content:'哈哈哈哈'
    //         })
    //     }
    //
    // }

    //=================================结构解析=======================
    copyData=(mydata)=>{
        let myData=[];
        num++;
        mydata.forEach((item,index)=>{
            // let time=moment(item.operationTime).format('YYYY-MM-DD HH:mm:ss');
            console.log(item.operTime);
            myData.push({
                key:index,
                staffName:item.staffName,
                operationName:item.operationName,
                operationTime:item.operTime,
                operationBody:item.operationBody,
                roleName:'',
                roleId:'',
                startTime:'',
                endTime:''
            })
        });
        if (num===1){
            this.setState({
                data:myData,
                copyData:myData
            })
        } else {
            this.setState({
                data:myData
            })
        }

    };
    componentDidMount() {
        // const myData=[];
        Axios.get("/OperatorLog/operatorLogAll").then(data=>{
           console.log(data.data.data);
            this.copyData(data.data.data)
        });

    }

    //下拉菜单被点击
    handleChange=(title)=>{
        console.log(title);
        this.setState({
            operationName:title
        })
    };
    onChange=(dates, dateStrings)=> {
        // console.log('From: ', dates[0], ', to: ', dates[1]);

        dateStrings[0] = moment(dateStrings[0]).format('YYYY-MM-DD' );
        dateStrings[1] = moment(dateStrings[1]).format('YYYY-MM-DD');
        console.log('From: ', dateStrings[0], ', to: ', dateStrings[1]);
        this.setState({
            startTime:dateStrings[0],
            endTime:dateStrings[1]
        })
    };
    mySearch=()=>{
        let myItem={staffName:this.staffName.current.state.value,operationName:this.state.operationName,startTime:this.state.startTime,endTime:this.state.endTime}
        console.log(myItem);
        Axios.post("/OperatorLog/operatorLog",myItem).then(res=>{
            console.log(res);
            this.setState({

            })
        })
    };
fuwei=()=>{
        this.setState({
            data:this.state.copyData
        })
};
    exportExcel = () => {
        const datas = this.state.data ? this.state.data : ""; //表格数据
        var option = {};
        let dataTable = []; //新建数组放数据
        console.log(datas);
        if (datas) {
            for (const data of datas) {
                console.log(data);
                if (data) {
                    let obj = {
                        key: data.key,
                        time: data.time,
                        man: data.man,
                        content: data.content,
                    };
                    dataTable.push(obj);
                }
            }
        }
        console.log(dataTable);
        option.fileName = "操作日志"; //文件名
        option.datas = [
            {
                sheetData: dataTable, //数据
                sheetName: "操作日志", //sheet名字 // sheetFilter: [dataTable.id, dataTable.name],//列过滤 // sheetFilter 列过滤(只有在 data 为 object 下起作用)(可有可无)
                sheetHeader: [
                    "类型",
                    "时间",
                    "操作人",
                    "操作内容",
                ], //// 第一行
            },
        ];
        var toExcel = new ExportJsonExcel(option);
        toExcel.saveExcel(); //保存
    };
    render() {
        //表格渲染

        const columns = [
            {
                title: '类型',
                dataIndex: 'operationName',
            },
            {
                title: '操作时间',
                dataIndex: 'operationTime',
            },
            {
                title: '操作人',
                dataIndex: 'staffName',
            },
            {
                title: '内容',
                dataIndex: 'operationBody',
            },
        ];
        return (

            <div id='Journal'>
                <div className='Journal_top'>
                    <form action="">
                        <label htmlFor="">操作人：</label>
                        <Input placeholder="Basic usage" style={{width:200,marginRight:20}} ref={this.staffName}/>
                        <RangePicker
                            defaultValue={[moment('2020-08-01', dateFormat), moment('2020-08/01', dateFormat)]}
                            format={dateFormat}
                            onChange={this.onChange}
                            style={{marginRight:20}}
                        />
                        <label htmlFor="">类型：</label>
                        <Select defaultValue={'选择操作类型'} style={{ width: 150 ,marginRight:25}} onChange={this.handleChange}>
                            <Option value={'后台用户登录'} key={1} title={'后台用户登录'}>后台用户登录</Option>
                            <Option value={'后台用户添加'} key={2} title={'后台用户添加'}>后台用户添加</Option>
                            <Option value={'后台用户查询'} key={3} title={'后台用户查询'}>后台用户查询</Option>
                            <Option value={'后台用户修改'} key={4} title={'后台用户修改'}>后台用户修改</Option>
                            <Option value={'后台用户查询'} key={5} title={'后台用户查询'}>后台用户查询</Option>
                        </Select>
                        <Button type="primary" size={this.state.size} style={{marginRight:20}} onClick={()=>{this.mySearch()}}>
                            查询
                        </Button>
                        <Button type="default" size={this.state.size} onClick={()=>{this.exportExcel()}}>
                            导出
                        </Button>
                        <Button type="default" size={this.state.size} onClick={()=>{this.fuwei()}}>
                            复位
                        </Button>
                    </form>
                </div>
                <div>
                    <Table columns={columns} dataSource={this.state.data} size="middle" />
                </div>,
            </div>
        )
    }
}

export default Journal
