import React, { Component } from "react";
import { Input, Table, Space, Button, Modal, Radio } from "antd";
import { ExclamationCircleOutlined } from "@ant-design/icons";
import Axios from "../../util/Axios";

const { Search } = Input;
const { confirm } = Modal;

export class SiteAccountManagement extends Component {
  state = {
    selectedRowKeys: [], // Check here to configure the default column
  };

  goBack() {
    this.props.history.push({ pathname: "/index/haoJie" });
    console.log("点击了");
  }

  add() {
    console.log(this.props.location.state.siteId);
    console.log(this.state.staffId);
    this.props.history.push({
      pathname: "/index/AddSiteMembers",
      state: {
        siteId: this.props.location.state.siteId,
        staffId: this.state.staffId,
      },
    });
  }

  constructor(props) {
    super(props);
    this.input_PeoplePhone = React.createRef();
    this.input_belongSite = React.createRef();
    this.input_sitePeopleName = React.createRef();
    this.input_addTime = React.createRef();
    this.state = {
      FT: {},
      staffId: {},
      selectedRowKeys: [],
      visible: false,
      data: [],
      PeoplePhone: "",
      belongSite: "",
      sitePeopleName: "",
      addTime: "",
      destroyOnClose: true,
      add: "add",
      columns: [
        {
          title: "站点员姓名",
          dataIndex: "name",
        },
        {
          title: "联系电话",
          dataIndex: "phone",
        },
        {
          title: "所属站点",
          dataIndex: "site",
        },
        // {
        //   title: "添加时间",
        //   dataIndex: "addTime",
        // },
        {
          title: "操作",
          dataIndex: "operation",
          render: (text, record) => (
            <Space size="middle">
              <span onClick={() => this.showModal(record.key)}>
                编辑
              </span>
              <span onClick={() => this.showConfirm([record.key])}>删除</span>
            </Space>
          ),
        },
      ],
    };
  }

  componentDidMount() {
    console.log(this.props.location.state.siteId);

    this.SiteFinsh();
  }
  // ===========================================数据渲染==================================================
  SiteFinsh() {
    Axios.post("/site/memberList", {
      siteId: this.props.location.state.siteId,
      siteName: "",
    })
      .then((data) => {
        console.log(data);
        console.log(data.data.data);
        console.log(this.props.location.state.siteId, "获取到的id");
        const myData = [];
        data.data.data.forEach((val, index) => {
          myData.push({
            key: index,
            name: val.name,
            phone: val.phone,
            siteId: this.props.location.state.siteId,
            site: val.site,
            addTime: val.addTime,
            staffId: val.staffId,
            operation: "查看",
          });
        });
        console.log(this);
        this.setState({
          data: myData,
        });
      })
      .catch((err) => {
        console.log(this.props.location.state.siteId, "获取到的id");
        console.log(err, "失败咯");
      });
  }

  //=======================================修改模态框设置================================================
  showModal = (mykey) => {
    console.log(mykey);
    console.log(this.state.data[mykey]);
    this.setState({
      FT: this.state.data[mykey],
      visible: true,
      phone: this.state.data[mykey].phone,
      name: this.state.data[mykey].name,
      site: this.state.data[mykey].site,
      // addTime: this.state.data[mykey].addTime,
      staffId: this.state.data[mykey].staffId,
      input_key: mykey,
      destroyOnClose: false,
    });
  };
  handleOk = (e) => {
    console.log(e);
    let inputK = this.state.input_key;
    let inputPP = this.input_PeoplePhone.current.state.value;
    let inputB = this.input_belongSite.current.state.value;
    let inputP = this.input_sitePeopleName.current.state.value;
    // let inputA = this.input_addTime.current.state.value;
    let items = [...this.state.data];
    console.log(items);

    items[this.state.input_key] = {
      key: inputK,
      name: inputP,
      phone: inputPP,
      site: inputB,
      // addTime: inputA,
      operation: "",
    };
    console.log(items[this.state.input_key]);
    let myitem = {
      name: inputP,
      phone: inputPP,
      staffId: this.state.staffId,
      siteId: this.state.FT.siteId,
      site: inputB,
      // addTime: inputA,
    };

    console.log(items);

    this.setState(
      {
        visible: false,
        data: items,
      },
      () => {
        console.log("触发回调函数");
        //==================编辑====================
        Axios.post("/site/updateMember", myitem)
          .then((data) => {
            console.log("====================");
            console.log(data);
          })
          .catch((err) => {
            console.log(err);
          });
      },

      () => {
        console.log("修改成功");
      }
    );
  };
  //========================================清除弹框内容======================================
  handleCancel = (e) => {
    console.log(e);
    this.setState({
      visible: false,
      destroyOnClose: true,
    });
  };

  clearContent() {
    this.setState({
      destroyOnClose: true,
    });
  }

  //======================删除弹框====================================
  showConfirm(mykey) {
    let that = this;
    this.setState({
      input_key: mykey,
    });
    confirm({
      title: "确认要删除该条数据?",
      icon: <ExclamationCircleOutlined />,
      content: "是否要删除呐~",
      onOk() {
        console.log(mykey);
        let myDelete = [];
        let items = [...that.state.data];
        console.log(items);
        for (let i = 0; i < mykey.length; i++) {
          myDelete.push(
            items[items.findIndex((item) => item.key === mykey[i])].staffId
          );
          items.splice(
            items.findIndex((item) => item.key === mykey[i]),
            1
          );
        }
        console.log(myDelete);
        Axios.post("/site/deleteMember", myDelete)
          .then((data) => {
            console.log(data, "成功删除");
          })
          .catch((err) => {
            console.log(err, "删除失败");
          });
        that.setState({
          data: items,
        });
      },
      onCancel() {
        console.log("Cancel");
      },
    });
  }

  //===================批量删除=================================
  onSelectChange = (selectedRowKeys) => {
    console.log("selectedRowKeys changed: ", selectedRowKeys);
    this.setState({ selectedRowKeys });
  };

  render() {
    const { selectedRowKeys } = this.state;
    let mydata = this.state.data;

    //====================================选择框设置==================================
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
      selections: [
        Table.SELECTION_ALL,
        Table.SELECTION_INVERT,
        {
          key: "odd",
          text: "选择奇数行",
          onSelect: (changableRowKeys) => {
            let newSelectedRowKeys = [];
            newSelectedRowKeys = changableRowKeys.filter((key, index) => {
              if (index % 2 !== 0) {
                return false;
              }
              return true;
            });
            this.setState({ selectedRowKeys: newSelectedRowKeys });
          },
        },
        {
          key: "even",
          text: "选择偶数行",
          onSelect: (changableRowKeys) => {
            let newSelectedRowKeys = [];
            newSelectedRowKeys = changableRowKeys.filter((key, index) => {
              if (index % 2 !== 0) {
                return true;
              }
              return false;
            });
            this.setState({ selectedRowKeys: newSelectedRowKeys });
          },
        },
      ],
    };

    return (
      <div>
        {/* 关键字 */}
        <div>
          关键字：
          <Search
            placeholder="模糊搜索"
            onSearch={(value) => {
              console.log(value);
              Axios.post("/site/memberList", {
                siteId: this.props.location.state.siteId,
                name: value,
              })
                .then((data) => {
                  console.log(data.data.data);
                  console.log(this.props.location.state.siteId, "获取id");
                  const myData = [];
                  data.data.data.forEach((val, index) => {
                    myData.push({
                      key: index,
                      name: val.name,
                      phone: val.phone,
                      siteId: this.props.location.state.siteId,
                      site: val.site,
                      addTime: val.addTime,
                      operation: "查看",
                    });
                  });
                  this.setState({
                    data: myData,
                  });
                })
                .catch((err) => {
                  console.log(err);
                });
            }}
            style={{ width: 200 }}
          />
        </div>
        <hr />
        {/* 表格 */}
        <Radio.Button
          value="default"
          onClick={() => {
            this.showConfirm(this.state.selectedRowKeys);
          }}
          style={{ marginRight: "5px", marginLeft: "5px" }}
        >
          批量删除
        </Radio.Button>
        <Button
          value="default"
          onClick={this.goBack.bind(this)}
          style={{ marginRight: "5px" }}
        >
          返回
        </Button>
        <Button
          value="default"
          onClick={this.add.bind(this)}
          style={{ marginRight: "5px" }}
        >
          添加
        </Button>
        <Table
          rowSelection={rowSelection}
          columns={this.state.columns}
          dataSource={mydata}
        />

        {/* ====================编辑弹框============== */}
        <div className="as">
          <Modal
            title="Basic Modal"
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            destroyOnClose={this.state.destroyOnClose}
          >
            <label>站点员姓名：</label>
            <Input
              defaultValue={this.state.name}
              ref={this.input_sitePeopleName}
            />
            <label>联系电话</label>
            <Input
              defaultValue={this.state.phone}
              ref={this.input_PeoplePhone}
            />
            <label>所属站点：</label>
            <Input defaultValue={this.state.site} ref={this.input_belongSite} />
            {/* <label>添加时间：</label>
            <Input
              type="date"
              defaultValue={this.state.addTime}
              ref={this.input_addTime}
            /> */}
          </Modal>
        </div>
      </div>
    );
  }
}

export default SiteAccountManagement;
