import React, { Component } from 'react'
import { Col, Form, Row, Select, Typography, Input, Button, Table,message } from "antd";
import Axios from '../../../util/Axios';




// import '../../css/F-DeliveryList/NewLogistics.css'

const { Option } = Select
const { Title } = Typography;
class newLogistics extends Component {
  constructor() {
    super()
    this.columns = [
      {
        title: '商品1级分类',
        dataIndex: 'classification1',
        editable: true,
        align: 'center'
      },
      {
        title: '商品2级分类',
        dataIndex: 'classification2',
        editable: true,
        align: 'center'
      },
      {
        title: '商品3级分类',
        dataIndex: 'classification3',
        editable: true,
        align: 'center'
      },
      {
        title: '数量',
        dataIndex: 'num',
        editable: true,
        align: 'center'
      },
    ];
    this.state = {
      aa: '',
      gradeOne: [],
      gradeTwo: [],
      gradeThree: [],
      supArr: [],
      allMsg: {}

    };
  }
  componentDidMount() {
    Axios.get('/logistics')
      .then(res => {
        console.log(res);

        if (res.status === 200) {
          let temp1 = [];
          res.data.data.categoryName.forEach(item => {
            temp1.push({ name: item.aname, id: item.categoryOne })
          })
          const id = 'id';
          const arr = temp1.reduce((all, next) => all.some((atom) => atom[id] === next[id]) ? all : [...all, next], []);

          this.setState({
            gradeOne: arr,
            allMsg: res.data.data
          })
        }


      })
  }

  //得到1级分类
  getOne = () => {
    let data = []
    this.state.gradeOne.forEach(item => {
      data.push(<Option key={item.id} value={item.id}>{item.name}</Option>)
    })
    return data
  }
  //得到2级分类
  getTwo = () => {
    let data = []
    this.state.gradeTwo.forEach(item => {
      data.push(<Option key={item.id} value={item.id}>{item.name}</Option>)
    })
    return data
  }
  getThree = () => {
    let data = []
    this.state.gradeThree.forEach(item => {
      data.push(<Option key={item.id} value={item.id}>{item.name}</Option>)
    })
    return data
  }

  //返回上一页
  goBackPage = () => {
    window.history.back(-1)
  };
  //一级分类发生改变  二级下拉的值跟随改变
  oneChange = (value) => {
    // console.log(value);
    let twoArr = this.state.allMsg.categoryName.filter(item => parseInt(value) === item.bparentId)
    let newTwoArr = []
    twoArr.forEach(item => {
      newTwoArr.push({ name: item.bname, id: item.categoryTwo })
    })
    // console.log(newTwoArr);
    const id = 'id';
    const arr = newTwoArr.reduce((all, next) => all.some((atom) => atom[id] === next[id]) ? all : [...all, next], []);
    this.setState({
      gradeTwo: arr,
      aa: '545454'
    })
  }
  //二级分类发生改变  三级下拉的值跟随改变
  twoChange = value => {
    let threeArr = this.state.allMsg.categoryName.filter(item => parseInt(value) === item.cparentId)
    // console.log(threeArr);
    let newThreeArr = []
    threeArr.forEach(item => {
      newThreeArr.push({ name: item.cname, id: item.categoryThree })
    })
    // console.log(newThreeArr);
    const id = 'id';
    const arr = newThreeArr.reduce((all, next) => all.some((atom) => atom[id] === next[id]) ? all : [...all, next], []);
    // console.log(arr);
    this.setState({
      gradeThree: arr
    })
  }
  //得到物料的种类
  getSup = () => {
    let supName = 'supName'
    console.log(this.state.allMsg.supName);
    if (this.state.allMsg.supName) {
      let data = this.state.allMsg.supName.reduce((all, next) => all.some((atom) => atom[supName] === next[supName]) ? all : [...all, next], []);
      console.log(data);
      let temp = []
      data.forEach(item => {
        temp.push(<Option key={item.supId} value={item.supId}>{item.supName}</Option>)
      })
      return temp
    }

  }
  render() {
    let dataSource = [
      {
        key: '0',
        classification1: <Form.Item
          name='categoryOne'
          rules={[
            {
              required: true,
              message: '请选择1级分类!',
            },
          ]}
        ><Select size="small" placeholder="选择1级分类" onChange={this.oneChange}>
            {this.getOne()}
          </Select></Form.Item>,
        classification2: <Form.Item
          name='categoryTwo'
          rules={[
            {
              required: true,
              message: '请选择2级分类!',
            },
          ]}
        ><Select size="small" placeholder="选择2级分类" onChange={this.twoChange} notFoundContent={<div>请选择上级分类</div>}>
            {this.getTwo()}
          </Select></Form.Item>,
        classification3: <Form.Item
          name='categoryThree'
          rules={[
            {
              required: true,
              message: '请选择3级分类!',
            },
          ]}
        ><Select size="small" placeholder="选择3级分类" notFoundContent={<div>请选择上级分类</div>}>
            {this.getThree()}
          </Select></Form.Item>,
        num: <Form.Item
          name='count'
          rules={[
            {
              required: true,
              message: '请输入数量!',
            },
          ]}
        >
          <Input size='small' placeholder="输入数量" />
        </Form.Item>
      },
    ];

    //提交新建物流单的信息
    const onFinish = values => {
      let that = this
      console.log('Received values of form: ', values);
      Axios.post('/logistics', values)
        .then(res => {
          if (res.status === 200) {
            that.props.history.push({
              pathname: "/index/List",
            })
            if (res.data.code === '0000') {
              message.success('平台派送成功');

            } else {
              message.error('派单失败！！请稍候再试');
            }
          }
          console.log(res);
        })
    };
    return (
      <div id='newLogistics' style={{ padding: '20px' }}>
        <div id='goBack' style={{
          paddingBottom: '15px',
          marginBottom: '10px',
          borderBottom: '1px solid #ccc',
        }}>
          <Button size="small" onClick={this.goBackPage}>返回</Button>
        </div>

        <Form
          name="Logistics_search"
          onFinish={onFinish}
        >
          <Title level={4}>收货人信息：</Title>
          <Row gutter={24}>
            <Col span={8} key='1'>
              <Form.Item
                name='userName'
                label='发货人:'
                rules={[
                  {
                    required: true,
                    message: '请输入发货人!',
                  },
                ]}
              >
                <Input size='small' />
              </Form.Item>
              <Form.Item
                name='userPhoneNumber'
                label='发货人电话:'
                rules={[
                  {
                    required: true,
                    message: '请输入发货人电话!',
                  },
                ]}
              >
                <Input size='small' />
              </Form.Item>
            </Col>
            <Col span={8} key='2'>
              <Form.Item
                name='consigneeName'
                label='收货人:'
                rules={[
                  {
                    required: true,
                    message: '请输入收货人!',
                  },
                ]}
              >
                <Input size='small' />
              </Form.Item>
              <Form.Item
                name='consigneePhoneNumber'
                label='收货人电话:'
                rules={[
                  {
                    required: true,
                    message: '请输入收货人电话!',
                  },
                ]}
              >
                <Input size='small' />
              </Form.Item>
            </Col>
            <Col span={8} key='3'>
              <Form.Item
                name='street'
                label='收货地址:'
                rules={[
                  {
                    required: true,
                    message: '请选择收货地址!',
                  },
                ]}
              >
                <Input size='small' placeholder="街道" />

              </Form.Item>
              <Form.Item
                name='consigneeAddress'
                label='详细地址:'
                rules={[
                  {
                    required: true,
                    message: '输入详细地址!',
                  },
                ]}
              >
                <Input size='small' placeholder="详细位置" />
              </Form.Item>
            </Col>
          </Row>
          <Title level={4}>派送选择：</Title>
          <Row gutter={24}>
            <Col span={8} key='4'>
              <Form.Item
                name='agingCode'
                label='选择时效产品：'
                rules={[
                  {
                    required: true,
                    message: '请选择开始时间!',
                  },
                ]}
              >
                <Select size="small" placeholder="选择时效产品">
                  <Option value="生鲜">生鲜</Option>
                  <Option value="常温">常温</Option>
                </Select>
              </Form.Item>
            </Col>
          </Row>
          <Title level={4}>订单详情：</Title>
          <Row gutter={24}>
            <Col span={15} key='5'>

              <Table
                pagination={false}
                locale={{ emptyText: '暂无数据' }}
                // components={components}
                rowClassName={() => 'editable-row'}
                bordered
                dataSource={dataSource}
                columns={this.columns}
                // pagination={paginationProps}
                size='small'
              />



            </Col>
            <Col offset='2' span={7} key='1'>
              {/* <Form.Item
                                name='count'
                                label='箱数:'
                                rules={[
                                    {
                                        required: true,
                                        message: '请输入箱数!',
                                    },
                                ]}
                            >
                                <Input size='small'/>
                            </Form.Item> */}
              <Form.Item
                name='heavy'
                label='重量:'
                rules={[
                  {
                    required: true,
                    message: '请输入重量!',
                  },
                ]}
              >
                <Input size='small' />
              </Form.Item>
              <Form.Item
                name='price'
                label='金额:'
                rules={[
                  {
                    required: true,
                    message: '请输入金额!',
                  },
                ]}
              >
                <Input size='small' />
              </Form.Item>
            </Col>
          </Row>
          <Title level={4}>物料情况：</Title>
          <Row gutter={24}>
            <Col span={8} key='1'>
              <Form.Item
                name='supId'
                label='选择消耗物料:'
                rules={[
                  {
                    required: true,
                    message: '请选择消耗物料!',
                  },
                ]}
              >
                <Select size="small" placeholder="选择消耗物料">
                  {this.getSup()}
                </Select>
              </Form.Item>
            </Col>
            <Col offset='3' span={8} key='2'>
              <Form.Item
                name='supCount'
                label='填写消耗数量:'
                rules={[
                  {
                    required: true,
                    message: '请输入消耗数量!',
                  },
                ]}
              >
                <Input size='small' />
              </Form.Item>

            </Col>
          </Row>
          <Row>
            <Col
              span={24}
              style={{
                textAlign: 'center',
              }}
            >
              <Button type="primary" htmlType="submit">
                确认
                            </Button>
            </Col>
          </Row>
        </Form>
      </div>
    )
  }
}

export default newLogistics