import React, { Component } from "react";
import { Input, Table, Space, Button, Modal } from "antd";
import { ExclamationCircleOutlined } from "@ant-design/icons";
import { withRouter ,Link } from "react-router-dom";
import Axios from "../../util/Axios";

const { Search } = Input;
const { confirm } = Modal;

class SiteManagement extends Component {
  constructor(props) {
    super(props);
    this.input_siteName = React.createRef();
    this.input_coordinate = React.createRef();
    this.input_address = React.createRef();
    this.input_phone = React.createRef();
    this.input_describe = React.createRef();

    this.state = {
      FT: {},
      selectedRowKeys: [],

      visible: false,
      data: [],
      siteName: "",
      coordinate: "",
      address: "",
      phone: "",
      describe: "",
      destroyOnClose: true,
      add: "add",
      sId: "",
      columns: [
        {
          title: "站点名称",
          dataIndex: "siteName",
        },
        {
          title: "地址",
          dataIndex: "siteAddress",
        },
        {
          title: "坐标",
          dataIndex: "siteCoordinate",
        },
        {
          title: "电话",
          dataIndex: "sitePhoneNumber",
        },
        {
          title: "描述",
          dataIndex: "siteRepresent",
        },
        {
          title: "操作",
          dataIndex: "operation",
          render: (text, record) => (
            <Space size="middle">
              <Button type="link" onClick={() => this.showModal(record.key)}>
                编辑
              </Button>
              <Button
                type="link"
                onClick={() => this.showConfirm([record.key])}
              >
                删除
              </Button>
              <Button
                type="link"
                onClick={() => this.btnFn(record)}
                replace="true"
              >
                查看
              </Button>
            </Space>
          ),
        },
      ],
    };
  }
  add() {
    this.props.history.push({
      pathname: "/AddSite",

    });
  }

  //=======================================修改模态框设置================================================
  showModal = (mykey) => {
    console.log(mykey);
    console.log(this.state.data[mykey]);

    this.setState({
      FT: this.state.data[mykey],
      visible: true,
      siteName: this.state.data[mykey].siteName,
      siteCoordinate: this.state.data[mykey].siteCoordinate,
      siteAddress: this.state.data[mykey].siteAddress,
      sitePhoneNumber: this.state.data[mykey].sitePhoneNumber,
      siteRepresent: this.state.data[mykey].siteRepresent,

      input_key: mykey,
      destroyOnClose: false,
    });
  };
  handleOk = (e) => {
    console.log(e);
    let inputK = this.state.input_key;
    let inputS = this.input_siteName.current.state.value;
    let inputC = this.input_coordinate.current.state.value;
    let inputA = this.input_address.current.state.value;
    let inputP = this.input_phone.current.state.value;
    let inputD = this.input_describe.current.state.value;
    let items = [...this.state.data];
    console.log(items);

    items[this.state.input_key] = {
      key: inputK,
      siteName: inputS,
      siteCoordinate: inputC,
      siteAddress: inputA,
      sitePhoneNumber: inputP,
      siteRepresent: inputD,
      operation: "",
    };
    let myitem = {
      address: inputA,
      coordinate: inputC,
      id: this.state.FT.siteId,
      name: inputS,
      phone: inputP,
      represent: inputD,
    };
    console.log(items);
    console.log(myitem.siteId);

    this.setState(
      {
        visible: false,
        data: items,
        destroyOnClose: true,
      },
      () => {
        console.log("触发回调函数");
        //==================编辑====================
        Axios.post("/site/update", myitem)
          .then((data) => {
            console.log("====================");
            console.log(data);
          })
          .catch((err) => {
            console.log(err);
          });
      },

      () => {
        console.log("修改成功");
      }
    );
  };

  //========================================清除弹框内容================================================
  handleCancel = (e) => {
    console.log(e);
    this.setState({
      visible: false,
      destroyOnClose: true,
    });
  };

  clearContent() {
    this.setState({
      destroyOnClose: true,
    });
  }
  //========================================路由跳转================================================
  btnFn = (record) => {
    console.log(record.siteId);
    this.props.history.push({
      pathname: "/index/SiteAccountManagement",
      state: { siteId: record.siteId },
    });
  };
  //======================删除弹框====================================
  showConfirm(mykey) {
    let that = this;
    this.setState({
      input_key: mykey,
    });
    confirm({
      title: "确认要删除该条数据?",
      icon: <ExclamationCircleOutlined />,
      content: "是否要删除呐~",
      onOk() {
        console.log(mykey);
        let items = [...that.state.data];
        let myDelete = [];
        console.log(items);
        for (let i = 0; i < mykey.length; i++) {
          myDelete.push(
            items[items.findIndex((item) => item.key === mykey[i])].siteId
          );
          items.splice(
            items.findIndex((item) => item.key === mykey[i]),
            1
          );
          console.log(myDelete);
        }

        console.log(myDelete);
        Axios.post("/site/delete", myDelete)
          .then((data) => {
            console.log(data, "成功删除");
          })
          .catch((err) => {
            console.log(err, "删除失败");
          });
        that.setState({
          data: items,
        });
      },
      onCancel() {
        console.log("Cancel");
      },
    });
  }

  componentDidMount() {
    this.Flash();
  }
  Flash() {
    //===================================数据渲染==================================
    Axios.post("/site/search", {siteName: "",})
      .then((data) => {
        console.log(data.data.data);

        const myData = [];
        data.data.data.forEach((val, index) => {
          myData.push({
            siteAddress: val.siteAddress,
            siteCoordinate: val.siteCoordinate,
            key: index,
            siteId: val.siteId,
            siteName: val.siteName,
            sitePhoneNumber: val.sitePhoneNumber,
            siteRepresent: val.siteRepresent,
            operation: "查看",
          });
          this.setState({
            sId: val.siteId,
          });
        });
        this.setState({
          data: myData,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  }
  state = {
    selectedRowKeys: [], // Check here to configure the default column
  };

  onSelectChange = (selectedRowKeys) => {
    // console.log("selectedRowKeys changed: ", selectedRows[0].siteId);
    this.setState({ selectedRowKeys });
    // console.log(selectedRowKeys);
  };

  render() {
    const { selectedRowKeys } = this.state;
    let mydata = this.state.data;

    //=================================选择单选框=================================
    const rowSelection = {
      selectedRowKeys,

      onChange: this.onSelectChange,
      selections: [
        Table.SELECTION_ALL,
        Table.SELECTION_INVERT,
        {
          key: "odd",
          text: "选择奇数行",
          onSelect: (changableRowKeys) => {
            let newSelectedRowKeys = [];
            newSelectedRowKeys = changableRowKeys.filter((key, index) => {
              if (index % 2 !== 0) {
                return false;
              }
              return true;
            });
            this.setState({ selectedRowKeys: newSelectedRowKeys });
          },
        },
        {
          key: "even",
          text: "选择偶数行",
          onSelect: (changableRowKeys) => {
            let newSelectedRowKeys = [];
            newSelectedRowKeys = changableRowKeys.filter((key, index) => {
              if (index % 2 !== 0) {
                return true;
              }
              return false;
            });
            this.setState({ selectedRowKeys: newSelectedRowKeys });
          },
        },
      ],
    };
    return (
      <div>
        {/* 关键字 */}
        <div>
          关键字：
          <Search
            placeholder="模糊搜索"
            onSearch={(value) => {
              console.log(value);
              Axios.post("/site/search", {siteName:value})
                .then((data) => {
                  console.log(data.data.data);
                  const myData = [];
                  data.data.data.forEach((val) => {
                    myData.push({
                      key: val.siteId,
                      siteName: val.siteName,
                      siteCoordinate: val.siteCoordinate,
                      siteAddress: val.siteAddress,
                      sitePhoneNumber: val.sitePhoneNumber,
                      siteRepresent: val.siteRepresent,
                      operation: "查看",
                    });
                  });
                  this.setState({
                    data: myData,
                  });
                })
                .catch((err) => {
                  console.log(err);
                });
            }}
            style={{ width: 200 }}
          />
        </div>
        <hr />
        {/* 表格 */}
        <Button
          value="default"
          onClick={() => {
            this.showConfirm(this.state.selectedRowKeys);
          }}
          style={{ marginRight: "5px", marginLeft: "5px" }}
        >
          批量删除
        </Button>
        <Link to={"/index/Site"}>
          <Button value="default" onClick={this.add.bind(this)}>
            添加
          </Button>
        </Link>

        <Table
          rowSelection={rowSelection}
          columns={this.state.columns}
          dataSource={mydata}
        />

        {/* ====================编辑弹框============== */}
        <div className="as">
          <Modal
            title="Basic Modal"
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            destroyOnClose={this.state.destroyOnClose}
          >
            <label>站点名称：</label>
            <Input
              defaultValue={this.state.siteName}
              ref={this.input_siteName}
            />
            <label>地址：</label>
            <Input
              defaultValue={this.state.siteAddress}
              ref={this.input_address}
            />
            <label>坐标：</label>
            <Input
              defaultValue={this.state.siteCoordinate}
              ref={this.input_coordinate}
            />
            <label>电话：</label>
            <Input
              defaultValue={this.state.sitePhoneNumber}
              ref={this.input_phone}
            />
            <label>描述：</label>
            <Input
              defaultValue={this.state.siteRepresent}
              ref={this.input_describe}
            />
          </Modal>
        </div>
      </div>
    );
  }
}

export default withRouter(SiteManagement);
